<template>
  <div>
    <h1 v-colored-text>Account aanmaken voor PSO/PAO/ISO</h1>
    <nn-stepper :steps="steps" :activeStep="1"></nn-stepper>
    <p>Er wordt een account aangemaakt voor <strong>{{ getFullName() }}</strong>. <br />Binnen enkele minuten
      versturen we een e-mail met inloggegevens voor {{ account.accountType }} naar <strong>{{ account.email }}</strong>.
    </p>
    <div class="o-layout">
      <div class="o-layout__row">
        <div class="o-layout__col-xs-6">
          <h3 class="u-margin-top u-margin-bottom--5">Details</h3>
          <table class="summary">
            <tr>
              <td><strong>Voornaam</strong></td>
              <td>{{account.firstName || '-'}}</td>
            </tr>
            <tr>
              <td><strong>Tussenvoegsel</strong></td>
              <td>{{account.middleName || '-'}}</td>
            </tr>
            <tr>
              <td><strong>Achternaam</strong></td>
              <td>{{account.lastName || '-'}}</td>
            </tr>
            <tr>
              <td><strong>Gebruikersnaam</strong></td>
              <td>{{account.userName || '-'}}</td>
            </tr>
            <tr>
              <td><strong>Telefoonnummer</strong></td>
              <td>{{account.phone || '-'}}</td>
            </tr>
            <tr>
              <td><strong>E-mailadres</strong></td>
              <td>{{account.email || '-'}}</td>
            </tr>
            <tr>
              <td><strong>Systeem</strong></td>
              <td>{{account.accountType || '-'}}</td>
            </tr>
          </table>
        </div>
      </div>
      <div class="o-layout__row u-margin-bottom--20">
        <div class="o-layout__col-xs-9">
          <h3 class="u-margin-top u-margin-bottom--5">Collectiviteiten</h3>
          <Datatable :rows="rows" :displayedColumns="columns" :border="false"></Datatable>
        </div>
      </div>
      <div class="o-layout__row">
        <div class="o-layout__col-xs-6">
          <nn-button variant="back" @click="backToStart">Terug naar home PSO/PAO/ISO</nn-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex';

  import ColoredText from "./../../../app-vue-shared/coloredText.directive.js";
  import Datatable from "./../../../app-vue-shared/datatable.js";
  import { stepsAddUser } from "./steps.js"

  export default {
    name: 'ConfirmAddAccount',
    components: { Datatable },
    directives: { ColoredText },
    data () {
      return {
        columns: { 'collective': 'Collectiviteit', 'companyName': 'Bedrijfsnaam', 'qNumber': 'Q-nummer', 'rcNumber': 'RC-nummer', 'mutation': 'Muteren' },
      };
    },
    methods: {
      gotoAddAccount () {
        this.$router.push({name: 'addAccount'});
      },
      backToStart () {
        this.$store.commit('resetAccount');
        this.$router.push({name: 'home'});
      },
      getFullName () {
        let lastName = (this.account.middleName && this.account.middleName.length > 0) ? this.account.middleName + ' ' + this.account.lastName : this.account.lastName;
        return (this.account.firstName + ' ' + lastName);
      },
      getMutationLabel (val) {
        switch (val) {
        case 'M':
          return 'Muteren';
        case 'I':
          return 'Inzien';
        default:
          return 'N.v.t.';
        }
      }
    },
    computed: {
      ...mapState([
        'account', 'collectives'
      ]),
      steps () {
        return stepsAddUser();
      },
      rows () {
        return this.collectives.map(_collective => {
          _collective.data.mutation = this.getMutationLabel(_collective.data.mutation);
          return _collective.data;
        });
      },
    },
    created () {
      if (this.account.firstName === null) {
        this.gotoAddAccount();
      }
    }
  }
</script>
