<template>
  <user-form
    title="Nieuwe gebruiker toevoegen"
    :status="status"
    :account="account"
    :onFormSubmit="submitForm"
    :onPropertyChanged="onPropertyChanged"
    :errorMessages="errorMessages"
  >
    <h2>Toekennen rechten voor beheer collectiviteiten</h2>

    <div class="o-layout">
      <div class="o-layout__row">
        <div class="o-layout__col-xs-12 collectivityTable">
          <Datatable
            :rows="collectives"
            :showPagination="false"
            :border="false"
            :displayedColumns="columns"
            :multiSelect="true"
            :selection="checkedCollectives"
            @updateSelection="updateSelection"
          ></Datatable>
        </div>
      </div>

      <div class="o-layout__row">
        <div
          class="o-layout__col-xs-12 u-margin-bottom--30"
        >Wanneer u op "account aanmaken" klikt wordt er een bevestigingsmail naar de gebruiker gestuurd.</div>
      </div>

      <div class="o-layout__row">
        <div class="o-layout__col-xs-6">
          <nn-button variant="secondary" @click="cancel">Annuleren</nn-button>
        </div>
        <div class="o-layout__col-xs-6 u-textalign-right">
          <nn-button type="submit">Account aanmaken</nn-button>
        </div>
      </div>
    </div>
  </user-form>
</template>

<script>
import { error } from '@nn/psdl-utility/src/console.js';

import UserForm from './Form';

import Api from '../../services/api.service.js';
import Alert from '../../../app-vue-shared/alert.js';
import Datatable from '../../../app-vue-shared/datatable';

const api = new Api();

export default {
  name: 'CreateAccount',
  components: { UserForm, Datatable, Alert },
  data () {
    return {
      account: {
        firstName: null,
        middleName: null,
        lastName: null,
        userName: null,
        phone: null,
        email: null
      },
      employerId: '',
      columns: {
        collective: 'Collectiviteiten',
        companyName: 'Bedrijfsnaam',
        qNumber: 'Q-nr',
        rcNumber: 'RC-nr',
        mutation: 'Rol'
      },
      collectives: [],
      checkedCollectives: [],
      authorisation: null,
      errorMessages: [],
      status: 'initial'
    };
  },
  methods: {
    onPropertyChanged (name, value) {
      this.account = {
        ...this.account,
        [name]: value,
      };
    },
    async loadData () {
      try {
        this.status = 'loading';
        const adminData = await api.getAdminData();
        this.employerId = adminData.employerId;
        this.collectives = adminData.collectives;
        this.authorisation = adminData.authorisations;
        this.status = 'done';
      } catch (e) {
        this.status = 'failed';
        if (e.messages) {
          this.errorMessages = e.messages
            .map(m => m.code === 'NOT-FOUND' ? 'U bent niet bevoegd een nieuwe gebruiker aan te maken.' : m.description);
        } else {
          this.errorMessages = ['Er is een onbekende fout opgetreden'];
          error(e);
        }
      }
    },
    async submitForm () {
      try {
        await api.createUser(
          this.account,
          this.employerId,
          this.checkedCollectives,
          this.authorisation
        );
        this.$router.push({ name: 'home' });
      } catch (e) {
        if (e.messages) {
          this.errorMessages = e.messages.map(m => m.description);
        } else {
          this.errorMessages = ['Er is een onbekende fout opgetreden'];
        }
      }
    },
    updateSelection (selection) {
      this.checkedCollectives = selection;
    },
    cancel () {
      this.$router.push({ name: 'home' });
    }
  },
  mounted () {
    this.loadData();
  }
};
</script>
