import Vue from "vue";
import VueRouter from 'vue-router'

import Home from '../pages/home';

import AddAccount from '../pages/account/add';
import EditAccount from '../pages/account/edit';

const createRouter = () => {
  if(!TESTING) {
    // Only add this line if we are not unit testing, otherwise mocking of $route and $router will not work as this line
    // makes them read-only
    Vue.use(VueRouter);
  }
  return new VueRouter({
    routes: [
      { path: '/', name: 'home', component: Home },
      { path: '/add', name: 'addAccount', component: AddAccount },
      { path: '/edit/:id', name: 'editAccount', component: EditAccount },
    ]
  });
}

export default createRouter;
