<template>
  <div>
    <label for="datepicker" class="c-form-label">
      Datum
    </label>
    <div class="o-flex">
      <div class="o-flex__col">
        <input id="datepicker" type="text" :name="name" placeholder="dd-mm-jjjj" ref="datepicker"
          class="c-form-text c-datepicker" v-validate="'required'" data-component="DatepickerField"
          @change="$emit('change', $event)" :value="value" data-date-picker-toggle="true" data-vv-validate-on="change"
          :data-end-date="monthsFromToday(6)" :data-start-date="monthsFromToday(-3)">
      </div>
      <div class="o-flex__col o-flex__col--auto  o-flex__col--icon">
        <span v-if="validationMessage" class="c-icon c-icon--pict-warning u-color--medium-orange u-font-size-m"></span>
        <span v-else class="c-icon u-color--medium-orange u-font-size-m c-icon--check" v-bind:class="[value ? '' : 'u-invisible']"></span>
      </div>
    </div>
    <div v-if="validationMessage" class="u-color--medium-orange u-font-bold u-margin-top--5"
      data-test="form-errorMessage">
      {{ validationMessage }}
    </div>
  </div>
</template>
<script>
  export default {
    props: {
      name: {
        type: String,
        required: true,
      },
      value: {
        type: String,
        requred: true,
      },
      validationMessage: {
        type: String,
        required: false,
      }
    },
    inject: ['$validator'],
    methods: {
      monthsFromToday(monthsFromToday){
        return createDate(monthsFromToday)
      },
    },
    mounted() {
      window.nn.initializeComponents(this.$refs.datepicker);
    }
  }

  const createDate = (monthsFromToday) => {
    const today = new Date();

    if(monthsFromToday && !isNaN(monthsFromToday)){
      const newDate = new Date(today.setMonth(today.getMonth()+monthsFromToday));
      return formattedDate(newDate);
    }
    return formattedDate(today);
  }

  const formattedDate = (unformattedDate) => {
    return unformattedDate.getFullYear() + '-' + (unformattedDate.getMonth() + 1) + '-' + unformattedDate.getDate();
  }
</script>