import Vue from 'vue';
import Vuex from 'vuex';

function defaultAccountState () {
  return {
    firstName: null,
    middleName: null,
    lastName: null,
    gender: null,
    userName: null,
    phone: null,
    email: null,
    accountType: 'PSO'
  };
}

function createStore () {
  Vue.use(Vuex);

  const state = {
    account: defaultAccountState(),
    collectives: []
  };

  const mutations = {
    updateAccount(state, account) {
      state.account = account;
    },
    updateAccountProperty(state, { name, value }) {
      state.account[name] = value;
    },
    addCollective (state, coll) {
      state.collectives.push(coll)
    },
    updateCollectiveValidity (state, { id, valid }) {
      const stateColl = state.collectives
        .find(coll => coll.id === id)
      stateColl.valid = valid
    },
    updateCollectiveProperty(state, { id, name, value }) {
      const stateColl = state.collectives
        .find(coll => coll.id === id)
      stateColl.data[name] = value
    },
    updateCollectives(state, collectives) {
      state.collectives = collectives;
    },
    resetAccount(state) {
      state.account = defaultAccountState();
      state.collectives = [];
    }
  };

  const actions = {};

  return new Vuex.Store({
    state,
    mutations,
    actions
  });
}

export default createStore;
