const postCodeRegex = /^[0-9]{4} *[a-zA-Z]{2}$/;

export default {
  addressLineOne: (value) => ({ valid: !!value.trim(), message: 'Vul je adres in' }),
  houseNumber: (value) => ({ valid: !!value.trim(), message: 'Vul je huisnummer in' }),
  street: (value) => ({ valid: !!value.trim(), message: 'Vul je straat in' }),
  city: (value) => ({ valid: !!value.trim(), message: 'Vul je straat plaatsnaam in' }),
  postalCode: (value) => {
    if (!value.trim()) {
      return { valid: false, message: 'Vul je postcode in.' };
    }
    return { valid: postCodeRegex.test(value), message: 'Vul een postcode van 4 cijfes en 2 letters in.' }
  }
}