import Http from "./http.service.js";

export class ApiErrorMessages extends Error {
  constructor(message, messages) {
    super(message);
    this.name = this.constructor.name;
    this.messages = messages;
  }
}

export default class Api {
  async searchUsers () {
    const url = '/api/x2/execute/WebEnvironment/SearchBusinessUserForBusinessCustomer';
    const body = {
      "SearchBusinessUser_Request": {
        "UsernameRequester": "?",
        "UserCategory": "DL",
        "Filters": {}
      }
    };
    const response = await this._executeRequest(url, body);
    const convertedResponse = this._handleReponse(response);

    if ((Number(convertedResponse.TotalNumberOfResults) > 0)) {
      if (Array.isArray(convertedResponse.Results.BusinessUser)) {
        return convertedResponse.Results.BusinessUser;
      }
      return [convertedResponse.Results.BusinessUser];

    }
    return [];
  }

  async getAdminData () {
    const url = '/api/x2/execute/WebEnvironment/GetUserDataBusinessCustomerForBusinessCustomer';
    const body = {
      'GetUserDataBusinessCustomer_Request': {
        'RequesterUsername': '?'
      }
    };

    const response = await this._executeRequest(url, body);
    const convertedResponse = this._handleReponse(response);
    let collectives = [];

    if (convertedResponse.LegacyDLData && convertedResponse.LegacyDLData.DataAuthorisation) {
      let dataAuthorisation = [];
      if (Array.isArray(convertedResponse.LegacyDLData.DataAuthorisation)) {
        dataAuthorisation = convertedResponse.LegacyDLData.DataAuthorisation;
      } else {
        dataAuthorisation = [convertedResponse.LegacyDLData.DataAuthorisation];
      }

      collectives = dataAuthorisation.map(_collective => ({
        collective: _collective.ID,
        companyName: _collective.CompanyName,
        rcNumber: _collective.PortfolioNumber,
        qNumber: _collective.EmployerQNumber,
        mutation: this._getMutationLabel(_collective.ModifyOrInsight)
      }));
    }

    return {
      collectives,
      employerId: convertedResponse.Employer.MandateForBusiness,
      authorisations: convertedResponse.LegacyDLData.Authorisations
    } ;
  }

  async getUser (userName) {
    const url = '/api/x2/execute/WebEnvironment/GetUserDataBusinessCustomerForBusinessCustomer';
    const body = {
      'GetUserDataBusinessCustomer_Request': {
        'RequesterUsername': '?',
        'Username': userName
      }
    };
    const response = await this._executeRequest(url, body);
    const convertedResponse = this._handleReponse(response);

    return {
      firstName: convertedResponse.UserData.FirstName || '',
      middleName: convertedResponse.UserData.FamilyNamePrefix || '',
      lastName: convertedResponse.UserData.FamilyName || '',
      gender: convertedResponse.UserData.Gender || '',
      userName: convertedResponse.UserData.Username || '',
      phone: convertedResponse.UserData.Telephonenumber || '',
      email: convertedResponse.UserData.EmailAddress || ''
    };
  }


  async getUserCollectives(user) {
    const url = '/api/x2/execute/WebEnvironment/GetUserDataBusinessCustomerForBusinessCustomer';
    const body = {
      "GetUserDataBusinessCustomer_Request": {
        "RequesterUsername": "?",
        "Username": user
      }
    };
    const response = await this._executeRequest(url, body);
    const convertedResponse = this._handleReponse(response);
    let collectives = [];

    if (convertedResponse.LegacyDLData.DataAuthorisation) {
      let dataAuthorisation = [];
      if (Array.isArray(convertedResponse.LegacyDLData.DataAuthorisation)) {
        dataAuthorisation = convertedResponse.LegacyDLData.DataAuthorisation;
      } else {
        dataAuthorisation = [convertedResponse.LegacyDLData.DataAuthorisation];
      }

      collectives = dataAuthorisation.map(_collective => ({
        collective: _collective.ID,
        companyName: _collective.CompanyName,
        rcNumber: _collective.PortfolioNumber,
        qNumber: _collective.EmployerQNumber,
        mutation: this._getMutationLabel(_collective.ModifyOrInsight)
      }));
    }

    return collectives;
  }

  createUser (userData, employerId, collectives, authorisation) {
    return this.modifyUser('Create', userData, employerId, collectives, authorisation);
  }

  updateUser (userData, employerId, collectives, authorisation) {
    return this.modifyUser('Update', userData, employerId, collectives, authorisation);
  }

  async modifyUser (action, userData, employerId, collectives, authorisation) {
    const url = '/api/x2/execute/WebEnvironment/HandleBusinessUserDLForBusinessCustomer';
    const dataAuthorisations = collectives.map(_collective => _collective.collective);

    const body = {
      "HandleBusinessUserDL_Request": {
        "UsernameRequester": "?",
        "Action": action,
        "Username": userData.userName,
        "DLPortalsEmployerId": employerId,
        "FirstName": userData.firstName,
        "FamilyName": userData.lastName,
        "FamilyNamePrefix": userData.middleName || '',
        "Gender": 'U',
        "EmailAddress": userData.email,
        "Telephonenumber": userData.phone || '',
        "Authorisations": authorisation,
        "DataAuthorisations": { "ID": dataAuthorisations }
      }
    };
    const response = await this._executeRequest(url, body);
    this._handleReponse(response);
  }

  async deleteUser (userName) {
    const url = '/api/x2/execute/WebEnvironment/DeleteBusinessUserDLForBusinessCustomer';
    const body = {
      "DeleteBusinessUserDL_Request": {
        "UsernameRequester": "?",
        "Username": userName
      }
    };
    const response = await this._executeRequest(url, body);
    this._handleReponse(response);
  }

  _executeRequest (url, body) {
    const http = new Http();
    return http.post(url, body);
  }

  _handleReponse (origResponse) {
    const httpStatusErrors = this._getHttpStatusErrorMessages(origResponse);
    if (httpStatusErrors.length) {
      throw new ApiErrorMessages('ApiErrorMessage', httpStatusErrors);
    }
    const convertedResponse = this._convertResponse(origResponse);
    if (convertedResponse.Result.Status === 'OK') {
      return convertedResponse;
    }

    if (convertedResponse.Result.Status === 'ERROR') {
      throw new ApiErrorMessages('ApiErrorMessage', this._getValidationErrorMessages(convertedResponse));
    }
  }

  _convertResponse (response) {
    try {
      return (JSON.parse(JSON.stringify(response[Object.keys(response)[0]]).replace(/{}/g, '""')));
    } catch (e) {
      return {};
    }
  }
  _getHttpStatusErrorMessages (err) {
    if (err instanceof Response && err.status === 401) {
      return [{ code: err.status, description: 'Niet geauthorizeerd om deze actie uit te voeren' }];
    }
    return [];
  }

  _getValidationErrorMessages (response) {
    const messages = {
      'DUPLICATE USERNAME': 'Deze gebruikersnaam is al reeds in gebruik. Kies een andere gebruikersnaam.',
      'NOT-FOUND': 'Gebruiker bestaat niet (meer).',
      'NOT-ALLOWED': 'Niet geauthorizeerd om deze actie uit te voeren',
      'DEFAULT': 'Er is een onbekende fout opgetreden. Probeer het nog eens.',
    }

    return Object.values(response.Result.ErrorList)
      .map(err => ({
        code: err.code,
        description: messages[err.code] || messages.DEFAULT
      }));
  }

  _getMutationLabel (val) {
    switch (val) {
    case 'M':
      return 'Muteren';
    case 'I':
      return 'Inzien';
    default:
      return 'N.v.t.';
    }
  }
}
