<template>
  <div>
    <label class="c-form-label">Adres</label>
    <div v-if="status === 'initial'" class="u-font-bold">
      <div v-if="address.houseNumber && address.postalCode">
        <span v-if="address.street">{{address.street}}</span>
        <span v-else>Postbus </span>                 
        {{address.houseNumber}} {{address.houseNumberAddition}}, {{address.city}}
      </div>
      <div v-else>Na het invoeren van uw postcode en huisnummer zal hier het adres getoond worden.</div>
    </div>
    <div v-if="status === 'loading'" class="u-font-bold"><nn-spinner></nn-spinner></div>
    <div v-if="status === 'done'" class="u-font-bold">
      <span v-if="address.street">{{address.street}}</span>
      <span v-else>Postbus </span>                 
      {{address.houseNumber}} {{address.houseNumberAddition}}, {{address.city}}
    </div>
    <div v-if="validationMessage"><alert :type="errorType">{{ validationMessage }}</alert></div>
  </div>
</template>
<script>
  export default {
    name: 'address-finder',
    data: function () {
      return {
        status: 'initial',
        validationMessage: null,
        errorType: 'error',
      }
    },
    props: {
      address: {
        type: Object,
        required: true,
      },
    },
    watch: {
      address: {
        handler: function (newValue, oldValue) {
          if (oldValue.houseNumber !== newValue.houseNumber ||
            oldValue.postalCode !== newValue.postalCode ||
            oldValue.houseNumberAddition !== newValue.houseNumberAddition) {
            this.onCheckAddress(newValue);
          }
        },
        deep: true,
      }
    },
    methods: {
      onCheckAddress: async function ({ houseNumber, postalCode, houseNumberAddition }) {
        if (houseNumber && postalCode) {
          try {
            this.status = 'loading';
            this.validationMessage = null;
            const {street, city} = await findAddress(houseNumber, postalCode, houseNumberAddition);
            this.status = 'done';
            this.$emit('onAddressFound', {street, city});
          } catch (err) {
            this.status = 'error';
            if (err.message === 'NOTFOUND') {
              this.validationMessage = 'De ingevoerde combinatie van "Huisnummer" en "postcode" is onbekend. Controleer uw gegevens of vul deze zelf verder aan.';
              this.errorType = 'info';
              this.$emit('onAddressNotFound');
            } else {
              this.validationMessage = 'Er is een onbekende fout opgetreden, probeer het later nog eens';
              this.errorType = 'error';
              this.$emit('onError', err);
            }
          }
        }
      }
    }
  };

  const findAddress = async (houseNumber, postalCode, houseNumberAddition) => {
    const payload = {
      "CheckPostalCodeREQ": {
        "PostalAddress": {
          "HouseNumber": houseNumber,
          "HouseNumberAddition": houseNumberAddition,
          "Postalcode": postalCode,
          "CountryCode": "nl",
        }
      }
    }

    const response = await fetch("/api/c0/execute/WebEnvironment/CheckPostalCode", {
      method: "POST",
      body: JSON.stringify(payload),
    });
    const json = await response.json();
    if (json.CheckPostalCodeRLY.ReplyEnvelope.returnCode === "OK") {
      const foundStreet = json.CheckPostalCodeRLY.PostalAddress.Street_tpg === 'POSTBUS' ? 'Postbus' : json.CheckPostalCodeRLY.PostalAddress.Street;
      return {
        street: foundStreet,
        city: json.CheckPostalCodeRLY.PostalAddress.City,
      };
    }
    throw new Error("NOTFOUND");
  };
</script>
