import Http from "./http.service.js";

export class ApiErrorMessages extends Error {
  constructor(message, messages) {
    super(message);
    this.name = this.constructor.name;
    this.messages = messages;
  }
}

export default class Api {
  constructor () {}

  async searchUsers (type, val) {
    const url = '/api/c2/execute/WebEnvironment/SearchBusinessUserForBusinessCustomer';
    const body = {
      "SearchBusinessUser_Request": {
        "UsernameRequester": "?",
        "UserCategory": "DL",
        "Filters": {
          "Filter": {
            "Element": type,
            "Value": val
          }
        }
      }
    };
    const response = await this._executeRequest(url, body);
    const convertedResponse = this._handleReponse(response);

    if ((Number(convertedResponse.TotalNumberOfResults) > 0)) {
      if (Array.isArray(convertedResponse.Results.BusinessUser)) {
        return convertedResponse.Results.BusinessUser;
      }
      return [convertedResponse.Results.BusinessUser];

    }
    return [];
  }

  async getUser (userName) {
    const url = '/api/c2/execute/WebEnvironment/GetUserDataBusinessCustomerForBusinessCustomer';
    const body = {
      "GetUserDataBusinessCustomer_Request": {
        "RequesterUsername": "?",
        "Username": userName
      }
    };
    const response = await this._executeRequest(url, body);
    const convertedResponse = this._handleReponse(response);

    let accountType = '';
    if (convertedResponse.LegacyDLData.Authorisations) {
      accountType = (convertedResponse.LegacyDLData.Authorisations.ISO === 'true' && accountType.length < 1) ? 'ISO' : '';
      accountType = (convertedResponse.LegacyDLData.Authorisations.PSO === 'true' && accountType.length < 1) ? 'PSO' : accountType;
      accountType = (convertedResponse.LegacyDLData.Authorisations.PAO === 'true' && accountType.length < 1) ? 'PAO' : accountType;
    }

    return {
      firstName: convertedResponse.UserData.FirstName || '',
      middleName: convertedResponse.UserData.FamilyNamePrefix || '',
      lastName: convertedResponse.UserData.FamilyName || '',
      gender: convertedResponse.UserData.Gender || '',
      userName: convertedResponse.UserData.Username || '',
      phone: convertedResponse.UserData.Telephonenumber || '',
      email: convertedResponse.UserData.EmailAddress || '',
      employerId: convertedResponse.Employer.MandateForBusiness || '',
      accountType: accountType,
      isAdmin: convertedResponse.UserData.IsAdmin,
    };
  }

  async getUserCollectives (userName) {
    const url = '/api/c2/execute/WebEnvironment/GetUserDataBusinessCustomerForBusinessCustomer';
    const body = {
      "GetUserDataBusinessCustomer_Request": {
        "RequesterUsername": "?",
        "Username": userName
      }
    };
    const response = await this._executeRequest(url, body);
    const convertedResponse = this._handleReponse(response);

    let collectives = [];

    if (convertedResponse.LegacyDLData.DataAuthorisation) {
      if (Array.isArray(convertedResponse.LegacyDLData.DataAuthorisation)) {
        collectives = convertedResponse.LegacyDLData.DataAuthorisation
          .map(collective => this._formatCollective(collective));
      } else {
        collectives.push(this._formatCollective(convertedResponse.LegacyDLData.DataAuthorisation));
      }
    }
    return collectives;
  }

  async createUser (userData) {
    const url = '/api/c2/execute/WebEnvironment/HandleBusinessUserDLForBusinessCustomer';
    const body = {
      "HandleBusinessUserDL_Request": {
        "UsernameRequester": "?",
        "Admin": "true",
        "Action": "Create",
        "Username": userData.userName,
        "FirstName": userData.firstName,
        "FamilyName": userData.lastName,
        "FamilyNamePrefix": userData.middleName || '',
        "Gender": "U",
        "EmailAddress": userData.email,
        "Telephonenumber": userData.phone || '',
        "Authorisations": {
          "ISO": ((userData.accountType === "ISO") ? "true" : "false"),
          "PSO": ((userData.accountType === "PSO") ? "true" : "false"),
          "PAO": ((userData.accountType === "PAO") ? "true" : "false")
        },
        "DataAuthorisations": []
      }
    }
    const response = await this._executeRequest(url, body);
    const convertedResponse = this._handleReponse(response);
    return (convertedResponse.DLPortalsEmployerId) ? Number(convertedResponse.DLPortalsEmployerId) : 0;
  }

  createOrUpdateCollectives (collectives, employerId) {
    collectives.forEach(collective => {
      this.createOrUpdateCollective(collective.data, employerId);
    });
  }

  async createOrUpdateCollective (collective, employerId) {
    const url = '/api/c2/execute/WebEnvironment/HandleDataAuthorisationDLForBusinessCustomer';
    const body = {
      "HandleDataAuthorisationDL_Request": {
        "UsernameRequester": "?",
        "DLPortalsEmployerId": employerId,
        "Action": "CreateOrUpdate",
        "DataAuthorisation": {
          "ID": collective.collective,
          "CompanyName": collective.companyName,
          "PortfolioNumber": collective.rcNumber,
          "EmployerQNumber": collective.qNumber,
          "ModifyOrInsight ": collective.mutation,
        }
      }
    };
    const response = await this._executeRequest(url, body);
    this._handleReponse(response);
  }

  deleteCollectives (collectives, employerId) {
    collectives.forEach(collective => {
      this.deleteCollective(collective, employerId);
    });
  }

  async deleteCollective (collective, employerId) {
    const url = '/api/c2/execute/WebEnvironment/HandleDataAuthorisationDLForBusinessCustomer';
    const body = {
      "HandleDataAuthorisationDL_Request": {
        "UsernameRequester": "?",
        "DLPortalsEmployerId": employerId,
        "Action": "Delete",
        "DataAuthorisation": {
          "ID": collective.collective
        }
      }
    };
    const response = await this._executeRequest(url, body);
    this._handleReponse(response);
  }

  async updateUser (userData, employerId) {
    const url = '/api/c2/execute/WebEnvironment/HandleBusinessUserDLForBusinessCustomer';
    const body = {
      "HandleBusinessUserDL_Request": {
        "UsernameRequester": "?",
        "Admin": userData.isAdmin,
        "Action": "Update",
        "Username": userData.userName.trim(),
        "DLPortalsEmployerId": employerId,
        "FirstName": userData.firstName,
        "FamilyName": userData.lastName,
        "FamilyNamePrefix": userData.middleName,
        "Gender": "U",
        "EmailAddress": userData.email,
        "Telephonenumber": userData.phone,
        "Authorisations": {
          "ISO": ((userData.accountType === "ISO") ? "true" : "false"),
          "PSO": ((userData.accountType === "PSO") ? "true" : "false"),
          "PAO": ((userData.accountType === "PAO") ? "true" : "false")
        },
        "DataAuthorisations": []
      }
    };
    const response = await this._executeRequest(url, body);
    this._handleReponse(response);
  }

  async deleteUser (userName) {
    const url = '/api/c2/execute/WebEnvironment/DeleteBusinessUserDLForBusinessCustomer';
    const body = {
      "DeleteBusinessUserDL_Request": {
        "UsernameRequester": "?",
        "Username": userName
      }
    };
    const response = await this._executeRequest(url, body);
    this._handleReponse(response);
  }

  async resetUser (userName) {
    const url = '/api/c2/execute/WebEnvironment/ResetPasswordForCall';
    const body = {
      "ResetPassword_Request": {
        "Party": {
          "UsernameRequester": "?",
          "Username": userName,
          "PartyType": "GEKO"
        }
      }
    };
    const response = await this._executeRequest(url, body);
    this._handleReponse(response);
  }

  createUniqueId () {
    let dt = new Date().getTime();
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      let r = (dt + Math.random() * 16) % 16 | 0;
      dt = Math.floor(dt / 16);
      return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
    });
  }

  _formatCollective (collective) {
    return {
      id: this.createUniqueId(),
      valid: true,
      data: {
        collective: collective.ID,
        companyName: collective.CompanyName,
        rcNumber: collective.PortfolioNumber,
        qNumber: collective.EmployerQNumber,
        mutation: collective.ModifyOrInsight,
      }
    }
  }

  _getHttpStatusErrorMessages (err) {
    if (err instanceof Response && err.status === 401) {
      return [{ code: err.status, description: 'Niet geauthorizeerd om deze actie uit te voeren' }];
    }
    return [];
  }

  _getValidationErrorMessages (response) {
    const messages = {
      'DUPLICATE USERNAME': 'Deze gebruikersnaam is al reeds in gebruik. Kies een andere gebruikersnaam.',
      'NOT-FOUND': 'Gebruiker bestaat niet (meer).',
      'NOT-ALLOWED': 'Niet geauthorizeerd om deze actie uit te voeren',
      'DEFAULT': 'Er is een onbekende fout opgetreden. Probeer het nog eens.',
    }

    return Object.values(response.Result.ErrorList)
      .map(err => ({
        code: err.code,
        description: messages[err.code] || messages.DEFAULT
      }));
  }

  _executeRequest (url, body) {
    const http = new Http();
    return http.post(url, body);
  }

  _handleReponse (origResponse) {
    const httpStatusErrors = this._getHttpStatusErrorMessages(origResponse);
    if (httpStatusErrors.length) {
      throw new ApiErrorMessages('ApiErrorMessage', httpStatusErrors);
    }
    const convertedResponse = this._convertResponse(origResponse);
    if (convertedResponse.Result.Status === 'OK') {
      return convertedResponse;
    }

    if (convertedResponse.Result.Status === 'ERROR') {
      throw new ApiErrorMessages('ApiErrorMessage', this._getValidationErrorMessages(convertedResponse));
    }
  }

  _convertResponse (response) {
    try {
      return JSON.parse(JSON.stringify(response[Object.keys(response)[0]]).replace(/{}/g, '""'));
    } catch (e) {
      return {};
    }
  }
}
