import template from './search.html';
import { mapActions, mapState } from 'vuex';
import {assign} from '@nn/psdl-utility/src/location.js';

export default {
  template,
  props: {
    config: Object
  },
  data () {
    return {
      searchType: null,
      searchPlaceholder: null,
      searchOptions: [
        {
          label: 'Account naam',
          searchPlaceholder: 'Zoek een bestaande gebruikersnaam',
          value: 'Username',
          disabled: false
        },
        {
          label: 'E-mailadres',
          searchPlaceholder: 'Zoek een bestaand E-mailadres',
          value: 'Emailaddress',
          disabled: false
        },
        {
          label: 'Collectiviteit',
          searchPlaceholder: 'Zoek een bestaande collectiviteit',
          value: 'DataAuthorisationID',
          disabled: false
        },
        {
          label: 'Golden Record ID',
          searchPlaceholder: 'Zoek een bestaand Golden Record ID',
          value: 'Customer_GR_ID',
          disabled: false
        },
      ],
      resultTableColumns: {
        'fullName': 'Naam',
        'userName': 'Account naam',
        'emailAddress': 'E-mailadres',
        'type': 'Type klant',
        ' ': ' ',
      },
    }
  },
  computed: {
    ...mapState([
      'message', 'status', 'businessUsers'
    ]),
    rows () {
      return (this.businessUsers || [])
        .map(user => {
          const type = user.DLPortalsEmployerId ? 'PSO/PAO/ISO' : 'Mijn NN zakelijk';
          return {
            'fullName': user.FullName,
            'userName': user.Username,
            'emailAddress': user.EmailAddress,
            'type': type,
            ' ': user
          };
        });
    }
  },
  methods: {
    ...mapActions([
      'findBusinessUsers',
      'clearState'
    ]),
    handleSubmit: async function (searchInput) {
      this.clearState();
      await this.findBusinessUsers({ searchType: this.searchType, searchInput })
    },
    goToEdit: function (user) {
      const redirectUrl = user.DLPortalsEmployerId ? `${this.config.redirectEditPSOPAO}${user.Username}` : `${this.config.redirectEditMijnNNZakelijk}${user.Username}`
      assign(redirectUrl);
    },
    onSearchTypeChange(value) {
      this.searchPlaceholder = this.searchOptions
        .find(option => option.value === value)
        .searchPlaceholder
    }
  },
  mounted () {
    this.searchType = this.searchOptions[0].value
    this.onSearchTypeChange(this.searchType)
  }
}
