export const findTheBusinesses = async (kvkNumber) => {
  const payload = createFindBusinessPayload(kvkNumber);
  const response = await fetch("/api/c0/execute/CustomerAdministration/FindKVKDataForAll", {
    method: "POST",
    body: JSON.stringify(payload),
  });
  const json = await response.json();

  if (json.FindKVKData_Response.Result.Status === "OK") {
    let result = json.FindKVKData_Response.BusinessPartner;
    if(result){
      result = !!result && result.constructor === Array ? result : [json.FindKVKData_Response.BusinessPartner];
    } else {
      result = [];
    }
    return result;
  }
  throw new Error(json.FindKVKData_Response);
};

const createFindBusinessPayload = (kvkNumber) => {
  return {
    "FindKVKData_Request": {
      "KVKNumber": kvkNumber,
    }
  }
};

export const sendRequestMovementForm = async (state) => {
  const payload = createFormPayload(state);
  const response = await fetch("/api/c0/execute/CustomerAdministration/RequestMovement2ForAll", {
    method: "POST",
    body: JSON.stringify(payload),
  });
  const json = await response.json();

  if (json.RequestMovement_Response.Result.Status === "OK") {
    return true;
  }
  throw new Error("Endpoint Error");
};

const createFormPayload = (state) => {
  const isoTimeStamp = createIsoTimeStamp();
  const messageId = createUniqueMessageId(state.kvk);

  return {
    "RequestMovement_Request": {
      "MessageID": messageId,
      "RequestMovement_Action": {
        "RequestEnvelope": {
          "TransactionDate": currentDate(),
          "TimeStamp": isoTimeStamp,
          "AuditTrail": {
            "EventType": "VerhuizingZakelijk",
            "From": "",
            "SourceSystem": "NN",
            "ProcessID": "",
            "TimeStampProcess": isoTimeStamp
          }
        },
        "BusinessPartner": {
          "Identification": {
            "IdentificationNumber": {
              "IdentificationType": "ZKVK",
              "IdentificationNumber": state.kvk
            }
          },
          "Organisation": {
            "OrganisationName": state.name,
            "Contactperson": {
              "Person": {
                "LastNamePrefix": state.contact.preposition,
                "LastName":  state.contact.lastName,
                "FirstName": state.contact.firstName,
              },
              "ContactInformation": {
                "TelephoneNumber": state.contact.phoneNumber,
                "EmailAddresses": {
                  "EmailAddress": state.contact.emailAddress
                }
              }
            }
          }
        },
        "Movement": {
          "MovementDate": convertToServerDate(state.startDate),
          "Addresses": createAddresses(state),
        }
      }
    }
  }
}

export const createAddresses = (state) => {
  const addresses = [];
  const mainAddress = {
    'AddressType': 'XXDEFAULT',
    'OldAddress': createAddress(state, 'oldAddress', 'mainAddress'),
  }

  if(state.addressesToModify.mainAddress) {
    mainAddress.NewAddress = createAddress(state, 'newAddress', 'mainAddress');
  }
  addresses.push(mainAddress);

  if(state.addressesToModify.postAddress) {
    addresses.push({
      'AddressType': 'Z_POSTAD',
      'NewAddress': createAddress(state, 'newAddress', 'postAddress'),
    })
  }

  return addresses
}

export const createAddress = (state, addressState, addressType) => {
  return {
    "PostalCode": state[addressState][addressType].postalCode,
    "HouseNumber": state[addressState][addressType].houseNumber,
    "HouseNumberAddition": state[addressState][addressType].houseNumberAddition,
    "Street": state[addressState][addressType].street,
    "City": state[addressState][addressType].city,
    "Country": state[addressState][addressType].countryCode,
  }
}

const convertToServerDate = (date) => {
  return date.split('-').reverse().join('-');
}

const currentDate = () => {
  return createIsoTimeStamp().split("T").shift();
}

const createIsoTimeStamp = () => {
  return new Date().toISOString();
}

const createUniqueMessageId = (idNumber) => {
  const currentTime = new Date().getTime();
  const randomNumber = Math.floor(Math.random()*(10000000-1+1)+1);
  return idNumber + currentTime + randomNumber;
}
