<template>
  <div>
    <div class="o-layout" v-if="contact">
      <h2>Wat zijn je contactgegevens?</h2>

      <div class="o-layout__row">
        <div class="o-layout__col-xs-9 o-layout__col-m-6">
          <div class="u-margin-bottom--30">
            <nn-input-field
              name="firstName"
              :value="contact.firstName"
              label="Voornaam"
              :required="true"
              @blur="validateAndUpdateFormElement($event, 'firstName')"
              @keyup-enter="validateAndUpdateFormElement($event, 'firstName')"
              :invalid="!isValidatedAndValid('firstName')"
              :error="validationMessage('firstName')"
            ></nn-input-field>
          </div> 
        </div>
      </div>

      <div class="o-layout__row">
        <div class="o-layout__col-xs-9 o-layout__col-m-6">
          <div class="u-margin-bottom--30">
            <nn-input-field
              name="preposition"
              :value="contact.preposition"
              label="Tussenvoegsels (optioneel)"
              @change="onUpdateContact"
            ></nn-input-field>
          </div>
        </div>
      </div>

      <div class="o-layout__row">
        <div class="o-layout__col-xs-9 o-layout__col-m-6">
          <div class="u-margin-bottom--30">
            <nn-input-field
              name="lastName"
              :value="contact.lastName"
              label="Achternaam"
              :required="true"
              @blur="validateAndUpdateFormElement($event, 'lastName')"
              @keyup-enter="validateAndUpdateFormElement($event, 'lastName')"
              :invalid="!isValidatedAndValid('lastName')"
              :error="validationMessage('lastName')"
            ></nn-input-field>
          </div> 
        </div>
      </div>

      <div class="o-layout__row">
        <div class="o-layout__col-xs-9 o-layout__col-m-6 ">
          <div class="u-margin-bottom--30">
            <nn-input-field
              name="phoneNumber"
              :value="contact.phoneNumber"
              label="Telefoonnummer (optioneel)"
              @change="onUpdateContact"
              @blur="validateAndUpdateFormElement($event, 'phoneNumber')"
              @keyup-enter="validateAndUpdateFormElement($event, 'phoneNumber')"
              :invalid="!isValidatedAndValid('phoneNumber')"
              :error="validationMessage('phoneNumber')"
            ></nn-input-field>
          </div>
        </div>
      </div>

      <div class="o-layout__row">
        <div class="o-layout__col-xs-9 o-layout__col-m-6">
           <div class="u-margin-bottom--30">
              <nn-input-field
                name="emailAddress"
                :value="contact.emailAddress"
                label="E-mailadres"
                :required="true"
                @blur="validateAndUpdateFormElement($event, 'emailAddress')"
                @keyup-enter="validateAndUpdateFormElement($event, 'emailAddress')"
                :invalid="!isValidatedAndValid('emailAddress')"
                :error="validationMessage('emailAddress')"
              ></nn-input-field>
          </div> 
        </div>
      </div>
    </div>

    <hr />

    <div class="u-margin-top--20">
      <nn-button variant="back" :to="'/businessdetails'">Terug</nn-button>
      <nn-button variant="next" name="forward" :to="'/review'" class="pull-right c-button--enabled" data-test="forwardButton">Volgende stap</nn-button>
    </div>
  </div>
</template>
<script>
  import { mapState, mapActions } from 'vuex';
  import ValidationMixin from '@nn/psdl-modules/src/app-vue-shared/validationMixin';
  import validations from './contactDetailsValidations'

  export default {
    mixins: [ ValidationMixin(validations) ],
    data: function () {
      return {
        selected: this.$store.state.contact.salutation,
      }
    },
    computed: {
      ...mapState([
        'status',
        'contact',
        'name',
      ]),
    },
    methods: {
      ...mapActions([
        'updateContact',
        'updateStatusSendForm',
      ]),
      onUpdateContact(value, event) {
        const newContact = { ...this.$store.state.contact, [event.target.name]: event.target.value };
        this.updateContact(newContact);
      },
      validateAndUpdateFormElement(event, name){
        this.validateFormElement(name, event.target.value)
        this.onUpdateContact(event.target.value, event)
      }
    },
    mounted() {
      if (!this.name) {
        this.$router.push('/');
      }
    },
    beforeRouteLeave(to, from, next) {
      if (to.name === 'review' && this.isFormValid) {
        this.validateForm(this.contact);
        if(this.isFormValid){
          this.updateStatusSendForm();
          next();
        }
      } else {
        next();
      }
    },
  }
</script>