<template>
  <div>
    <div class="u-color-background--ultra-light-grey u-padding--30 u-margin-bottom--15">
      <form @submit.prevent="onSearchBusiness">
        <div class="u-margin-bottom--15">
          <nn-input-field
            name="searchInput"
            v-model="formKvkNumber"
            placeholder="KVK-nummer"
            label="Zoek bedrijf in het KvK Handelsregister"
            :hasSearchButton="true"
            searchButtonType="submit"
          ></nn-input-field>
        </div>
      </form>
      <nn-link href="#" @click.prevent="nextPageWithoutKvk()" data-test="noKvK">Ik heb geen KVK-nummer</nn-link>
    </div>

    <div v-if="searchInputError && status.find !== 'error'">
      <alert :type="'error'">{{searchInputError}}</alert>
    </div>
    <div v-if="status.find === 'loading'">
      <nn-spinner></nn-spinner>
    </div>
    <div v-if="status.find ==='error'">
      <alert :type="'error'">Er is een fout opgetreden, probeer het later nog eens.</alert>
    </div>
    <div v-if="status.find === 'not-found'">
        <alert :type="'error'">Dit KVK-nummer is niet helaas niet gevonden.</alert>
    </div>
    <div v-if="status.find === 'done'" class="u-margin-top--35">
      <h2 v-coloredText class="u-margin--none">Gevonden bedrijven in het KvK Handelsregister</h2>
      <p class="u-margin-bottom--30"><span class="u-font-bold">Let op:</span> het adres dat getoond wordt is het adres zoals het bekend is bij de Kamer van Koophandel. Geef je nieuwe adres ook aan ons door.</p>
      <div class="c-scrollablecontainer--horizontal">
        <table class="c-table">
          <thead class="c-table__header">
            <tr class="c-table__header__row">
              <th class="c-table__header__cell">Bedrijfsnaam</th>
              <th class="c-table__header__cell">
                Adres in het KvK Handelsregister
                <context-dialog side="bottom">
                  <template>
                    <h3 v-coloredText>Adres in KvK Handelsregister</h3>
                    <p>
                      We tonen ter identificatie hier het adres zoals het bekend is bij de Kamer van Koophandel.
                      Dit is niet het adres zoals bij Nationale-Nederlanden bekend is.
                    </p>
                  </template>
                </context-dialog>
              </th>
              <th class="c-table__header__cell">KVK-nummer</th>
              <th class="c-table__header__cell"></th>
            </tr>
          </thead>
          <tbody class="c-table__body">
            <tr v-for="row in results" class="c-table__body__row c-table__body__row--hover">
              <td class="c-table__body__cell c-table__body__cell__linkblock">
                <a href="#" @click.prevent="onSelected(row)" class="c-table__body__cell__link"><span class="u-screen-reader">Selecteer address</span></a>
                <span>{{ row.Organisation.OrganisationName }}</span>
              </td>
              <td class="c-table__body__cell c-table__body__cell__linkblock">
                <a href="#" @click.prevent="onSelected(row)" class="c-table__body__cell__link"><span class="u-screen-reader">Selecteer address</span></a>
                <span class="u-font-bold">Vestigingsadres<br></span>                
                <span v-if="typeof(row.Address.Street) !== 'object'">{{ row.Address.Street }} </span> 
                <span v-else>Postbus </span>
                <span v-if="typeof(row.Address.HouseNumber) !== 'object'">{{ row.Address.HouseNumber }}</span>
                <span v-else>{{ row.Address.POBox }}</span>
                <span v-if="typeof(row.Address.HouseNumberAddition) !== 'object'">{{ row.Address.HouseNumberAddition }}<br></span>
                <span v-else><br></span>               
                <span class="u-margin-bottom--10">{{ row.Address.PostalCode }}, {{ row.Address.City }}</span>
              </td>
              <td class="c-table__body__cell c-table__body__cell__linkblock">
                <a href="#" @click.prevent="onSelected(row)" class="c-table__body__cell__link" data-test="selectBusiness"><span class="u-screen-reader">Selecteer address</span></a>
                <span>{{ kvkNumber ? kvkNumber : kvk }}</span>
              </td>
              <td class="c-table__body__cell c-table__body__cell__linkblock">
                <nn-link href="#" alt="businessdetails link" @click.prevent="onSelected(row)">Selecteer</nn-link>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
  import { mapState, mapActions } from 'vuex';
  import contextDialog from '../components/contextDialog'

  export default {
    components: { contextDialog },
    data: function () {
      return {
        formKvkNumber: '',
        searchInputError: '',
        kvkNumber: '',
      }
    },
    computed: {
      ...mapState([
        'newAddress',
        'results',
        'status',
        'kvk',
      ]),
    },
    methods: {
      ...mapActions([
        'findBusinesses',
        'setInitialCompanyData',
        'resetState',
      ]),
      onSearchBusiness: async function (event) {
        if(event && event.target.elements.searchInput.value.trim()){
          this.resetState();
          this.searchInputError = '';
          const searchInput = event.target.elements.searchInput.value.trim();
          if(this.validateSearchInput(searchInput)){
            this.kvkNumber = searchInput;
            await this.findBusinesses(searchInput);
          } else {
            this.searchInputError = 'Controleer het KvK nummer. Een juist KvK nummer heeft 8 getallen.';
          }
        }
      },
      onSelected: function (selected) {
        this.setInitialCompanyData(selected);
        this.$router.push({name:'businessdetails'});
      },
      nextPageWithoutKvk: function () {
        this.resetState();
        this.$router.push({name:'businessdetails'});
      },
      beforeMount: function(){
        this.resetState();
      },
      validateSearchInput: function(searchInput){
        return !isNaN(searchInput) && searchInput.length === 8;
      },
    },
  }
</script>