<template>
  <form
    class="userform"
    @submit.prevent="_validateFormAndSubmit"
    novalidate
  >
    <h1>{{title}}</h1>

    <alert v-if="errorMessages.length > 0" type="error" class="u-margin-bottom--30">
      <span v-for="(error, i) in errorMessages" :key="i">{{error}}</span>
    </alert>

    <nn-spinner v-if="status === 'loading'"></nn-spinner>

    <div v-if="status === 'done'">
      <fieldset class="c-form-fieldset">
        <div class="o-layout">
          <div class="o-layout__row">
            <div class="o-layout__col-xs-6 o-layout__col-m-3">
              <div class="u-margin-bottom--30">
                <nn-input-field
                  label="Voornaam"
                  name="firstName"
                  :value="account.firstName"
                  @input="onPropertyChanged('firstName', $event)"
                  @blur="validateFormElement('firstName', $event.target.value)"
                  @keydown-enter="validateFormElement('firstName', $event.target.value)"
                  :invalid="!isValidatedAndValid('firstName')"
                  :error="validationMessage('firstName')"
                ></nn-input-field>
              </div>
            </div>

            <div class="o-layout__col-xs-6 o-layout__col-m-3">
              <div class="u-margin-bottom--30">
                <nn-input-field
                label="Tussenvoegsel (optioneel)"
                name="middleName"
                :value="account.middleName"
                @input="onPropertyChanged('middleName', $event)"
              ></nn-input-field>
              </div>
            </div>
          </div>
          <div class="o-layout__row">
            <div class="o-layout__col-s-6">

              <div class="u-margin-bottom--30">
                <nn-input-field
                  label="Achternaam"
                  name="lastName"
                  :value="account.lastName"
                  @input="onPropertyChanged('lastName', $event)"
                  @blur="validateFormElement('lastName', $event.target.value)"
                  @keydown-enter="validateFormElement('lastName', $event.target.value)"
                  :invalid="!isValidatedAndValid('lastName')"
                  :error="validationMessage('lastName')"
                  required
                ></nn-input-field>
              </div>

              <div class="u-margin-bottom--30">
                <nn-input-field
                  label="Gebruikersnaam"
                  name="userName"
                  :value="account.userName"
                  @input="onPropertyChanged('userName', $event)"
                  @blur="validateFormElement('userName', $event.target.value)"
                  @keydown-enter="validateFormElement('userName', $event.target.value)"
                  :invalid="!isValidatedAndValid('userName')"
                  :error="validationMessage('userName')"
                  :minlength="2"
                  :maxlength="50"
                  required
                  :readonly="account.userName && account.userName.length > 0"
                ></nn-input-field>
              </div>

              <div class="u-margin-bottom--30">
                <nn-input-field
                  label="Telefoonnummer (optioneel)"
                  name="phone"
                  :value="account.phone"
                  @input="onPropertyChanged('phone', $event)"
                  type="tel"
                ></nn-input-field>
              </div>

              <div class="u-margin-bottom--30">
                <nn-input-field
                  label="E-mailadres"
                  name="email"
                  :value="account.email"
                  @input="onPropertyChanged('email', $event)"
                  @blur="validateFormElement('email', $event.target.value)"
                  @keydown-enter="validateFormElement('email', $event.target.value)"
                  :invalid="!isValidatedAndValid('email')"
                  :error="validationMessage('email')"
                  type="email"
                  required
                ></nn-input-field>
              </div>
            </div>
          </div>
        </div>
      </fieldset>
     <slot></slot>
    </div>
  </form>
</template>

<script>
import Alert from '@nn/psdl-modules/src/app-vue-shared/alert';
import ValidationMixin from '@nn/psdl-modules/src/app-vue-shared/validationMixin';
import validations from './validations';

export default {
  name: 'userform',
  components: { Alert },
  mixins: [ ValidationMixin(validations) ],
  props: {
    onPropertyChanged: Function,
    title: String,
    status: String,
    onFormSubmit: Function,
    account: Object,
    errorMessages: Array,
  },
  methods: {
    async _validateFormAndSubmit () {
      this.validateForm(this.account);
      if (this.isFormValid) {
        this.onFormSubmit();
      } else {
        await this.$nextTick();
        this.$el.querySelector('form.userform input.c-form-text--error').focus();
      }
    },
  }
}
</script>