<template>
  <div>
    <div v-if="status.sendForm == 'initial' || status.sendForm == 'error'">
      <h4>Bedrijfsgegevens</h4>
      <div class="o-layout u-margin-bottom--30">
        <div class="o-layout__row">
          <div class="o-layout__col-xs-6 o-layout__col-m-4">
            Bedrijfsnaam:
          </div>
          <div class="o-layout__col-xs-6 o-layout__col-m-6">
            {{name}}
          </div>
        </div>
        <div class="o-layout__row" v-if="kvk !== ''">
          <div class="o-layout__col-xs-6 o-layout__col-m-4">
            KVK-nummer:
          </div>
          <div class="o-layout__col-xs-6 o-layout__col-m-3">
            {{kvk}}
          </div>
        </div>
        <div class="o-layout__row">
          <div class="o-layout__col-s-12">
            <alert v-if="status.sendForm === 'error'" :type="'error'" class="u-margin-bottom--30">
              <span>Versturen van uw formulier is mislukt. Probeer het alstublieft nogmaals.</span>
            </alert>
          </div>
        </div>
        <div v-if="addressesToModify.mainAddress || addressesToModify.postAddress && !addressesToModify.mainAddress">
          <div class="o-layout__row">
            <div v-if="addressesToModify.mainAddress" class="o-layout__col-xs-6 o-layout__col-m-4">
              Oud vestigingsadres:
            </div>
            <div v-else class="o-layout__col-xs-6 o-layout__col-m-4">
              Vestigingsadres:
            </div>
            <div class="o-layout__col-xs-6 o-layout__col-m-6">
              <div>{{oldAddress.mainAddress.street}} {{oldAddress.mainAddress.houseNumber}} {{oldAddress.mainAddress.houseNumberAddition}}
              </div>
              <div>{{oldAddress.mainAddress.postalCode}}, {{oldAddress.mainAddress.city}}</div>
            </div>
          </div>
          <div v-if="addressesToModify.mainAddress" class="o-layout__row">
            <div class="o-layout__col-xs-6 o-layout__col-m-4">
              Nieuw vestigingsadres:
            </div>
            <div class="o-layout__col-xs-6 o-layout__col-m-6">
              <div>{{newAddress.mainAddress.street}} {{newAddress.mainAddress.houseNumber}} {{newAddress.mainAddress.houseNumberAddition}}</div>
              <div>{{newAddress.mainAddress.postalCode}}, {{newAddress.mainAddress.city}}</div>
            </div>
          </div>
        </div>
        <div v-if="addressesToModify.postAddress">
          <div class="o-layout__row">
            <div class="o-layout__col-xs-6 o-layout__col-m-4">
              Nieuw postadres:
            </div>
            <div class="o-layout__col-xs-6 o-layout__col-m-6">
              <div>{{newAddress.postAddress.street}} {{newAddress.postAddress.houseNumber}} {{newAddress.postAddress.houseNumberAddition}}</div>
              <div>{{newAddress.postAddress.postalCode}}, {{newAddress.postAddress.city}}</div>
            </div>
          </div>
        </div>
      </div>

      <h4>Contactgegevens</h4>
      <div class="o-layout u-margin-bottom--30">
        <div class="o-layout__row">
          <div class="o-layout__col-xs-6 o-layout__col-m-4">
            Voornaam:
          </div>
          <div class="o-layout__col-xs-6 o-layout__col-m-3">
            {{contact.firstName}}
          </div>
        </div>
        <div class="o-layout__row">
          <div class="o-layout__col-xs-6 o-layout__col-m-4">
            Achternaam:
          </div>
          <div class="o-layout__col-xs-6 o-layout__col-m-6">
            {{contact.preposition}} {{contact.lastName}}
          </div>
        </div>
        <div v-if="contact.phoneNumber !== ''" class="o-layout__row">
          <div class="o-layout__col-xs-6 o-layout__col-m-4">
            Telefoonnummer:
          </div>
          <div class="o-layout__col-xs-6 o-layout__col-m-3">
            {{contact.phoneNumber}}
          </div>
        </div>
        <div class="o-layout__row">
          <div class="o-layout__col-xs-6 o-layout__col-m-4">
            E-mailadres:
          </div>
          <div class="o-layout__col-xs-6 o-layout__col-m-6">
            {{contact.emailAddress}}
          </div>
        </div>
      </div>

      <h4>Ingangsdatum</h4>
      <div class="o-layout u-margin-bottom--30">
        <div class="o-layout__row">
          <div class="o-layout__col-xs-6 o-layout__col-m-3">
            {{startDate}}
          </div>
        </div>
      </div>

      <hr />
      <div class="u-margin-top--20">
        <nn-button variant="back" :to="'/contactdetails'">Terug</nn-button>
        <nn-button variant="next" class="pull-right" @click="sendForm()">Verzenden</nn-button>
      </div>
    </div>

    <div v-if="status.sendForm == 'loading'">
      <nn-spinner></nn-spinner>
    </div>

    <div v-if="status.sendForm == 'done'">
      <h4>Uw bedrijfsgegevens zijn ontvangen</h4>
      <div class="o-layout u-margin-bottom--20">
        <div class="o-layout__row">
          <div class="o-layout__col-xs-6 o-layout__col-m-6">
            We hebben uw nieuwe bedrijfsgegevens in goede orde ontvangen en zullen deze zo snel mogelijk verwerken. U
            ontvangt binnen 5 werkdagen een brief met een bevestiging.
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { mapState, mapActions } from 'vuex';

  export default {
    computed: {
      ...mapState([
        'addressesToModify',
        'newAddress',
        'oldAddress',
        'status',
        'startDate',
        'contact',
        'name',
        'kvk'
      ]),
    },
    methods: {
      ...mapActions([
        'sendFinalForm',
      ]),
      sendForm(){
        this.sendFinalForm();
      }
    },
    mounted(){
      if (!this.name) {
        this.$router.push('/');
      }
    },
  }
</script>