<template>
  <div>
    <div class="o-layout__row">
      <div class="o-layout__col-xs-10 o-layout__col-s-8 o-layout__col-m-6 o-layout__col-l-8 u-margin-bottom--30">
        <nn-dropdown
          :options="countries"
          :value="address.countryCode"
          label="Land"
          @change="onUpdateSelectedCountry"
        ></nn-dropdown>
      </div>
    </div>
    <div v-if="address.countryCode!=='NL'" class="o-layout__row">
      <div class="o-layout__col-xs-10 u-margin-bottom--30">
        <nn-input-field
          name="addressLineOne" 
          :id="'addressLineOne-' + blockLabelsId"
          label="Adres"
          v-model="addressLineOne"
          @change="onUpdateForeignAddressField"
          @blur="validateFormElement('addressLineOne', $event.target.value)"
          @keydownEnter="validateFormElement('addressLineOne', $event.target.value)"
          :invalid="!isValidatedAndValid('addressLineOne')"
          :error="validationMessage('addressLineOne')"
          required
          class="u-margin-bottom"
        ></nn-input-field>
        <nn-input-field
          name="addressLineTwo" 
          :id="'addressLineTwo-' + blockLabelsId"
          v-model="addressLineTwo"
          @change="onUpdateForeignAddressField"
          :addValidationIconPlaceholder="true"
        ></nn-input-field>
      </div>
    </div>
    <div class="o-layout__row">
      <div class="o-layout__col-xs-10 o-layout__col-s-8 o-layout__col-m-6 o-layout__col-l-8 u-margin-bottom--30">
        <nn-input-field
          name="postalCode"
          label="Postcode"
          :id="'postalCode-' + blockLabelsId"
          :value="address.postalCode"
          required
          @blur="validatePostCodeAfterCheck('postalCode', $event.target.value)"
          @keydownEnter="validatePostCodeAfterCheck('postalCode', $event.target.value)"
          @change="onUpdateAddressField"
          :invalid="!isValidatedAndValid('postalCode')"
          :error="validationMessage('postalCode')"
        ></nn-input-field>
      </div>
    </div>
    <div v-if="address.countryCode==='NL'">
      <div class="o-layout__row u-flex u-flex--end">
        <div class="o-layout__col-xs-5 o-layout__col-s-8 o-layout__col-m-6 o-layout__col-l-4 u-margin-bottom--30">
          <nn-input-field
            name="houseNumber"
            label="Huisnummer"
            :id="'houseNumber-' + blockLabelsId"
            required
            :value="address.houseNumber"
            @change="onUpdateAddressField"
            @blur="validateFormElement('houseNumber', $event.target.value)"
            @keydownEnter="validateFormElement('houseNumber', $event.target.value)"
            :invalid="!isValidatedAndValid('houseNumber')"
            :error="validationMessage('houseNumber')"
          ></nn-input-field>
        </div>
        <div class="o-layout__col-xs-5 o-layout__col-s-4 o-layout__col-m-6 o-layout__col-l-4 u-margin-bottom--30">
          <nn-input-field
            name="houseNumberAddition" 
            :id="'addressLineTwo-' + blockLabelsId"
            label="Toevoeging (optioneel)"
            :value="addressLineTwo"
            @change="onUpdateForeignAddressField"
            :addValidationIconPlaceholder="true"
          ></nn-input-field>
        </div>        
      </div>
      <div class="o-layout__row">
        <div class="o-layout__col-xs-12 div u-margin-bottom--30">
          <address-finder 
            :address="address"
            @onAddressFound="onAddressFound"
            @onError="onAddressFoundError"
            @onAddressNotFound="onAddressNotFound"
          ></address-finder>
        </div>
      </div>

      <div v-if="addressNotFound" >
        <div class="o-layout__row">
          <div class="o-layout__col-xs-10 o-layout__col-s-8 o-layout__col-m-6 o-layout__col-l-8 u-margin-bottom--30">
            <nn-input-field
              name="street"
              label="Straat"
              :id="'street-' + blockLabelsId"
              :value="address.street"
              @change="onUpdateAddressField"
              required
              @blur="validateFormElement('street', $event.target.value)"
              @keydownEnter="validateFormElement('street', $event.target.value)"
              :invalid="!isValidatedAndValid('street')"
              :error="validationMessage('street')"
            ></nn-input-field>
          </div>
        </div> 
      </div>
    </div>
    <div v-if="addressNotFound || address.countryCode!=='NL'" class="o-layout__row" >
      <div class="o-layout__col-xs-10 o-layout__col-s-8 o-layout__col-m-6 o-layout__col-l-8 u-margin-bottom--30">
        <nn-input-field
          name="city"
          label="Plaatsnaam"
          :id="'city-' + blockLabelsId" 
          :value="address.city"
          @change="onUpdateAddressField"
          required
          @blur="validateFormElement('city', $event.target.value)"
          @keydownEnter="validateFormElement('city', $event.target.value)"
          :invalid="!isValidatedAndValid('city')"
          :error="validationMessage('city')"
        ></nn-input-field>
      </div>
    </div>
    <div v-if="address.countryCode!=='NL'" class="o-layout__row" >
      <div class="o-layout__col-xs-10 o-layout__col-s-8 o-layout__col-m-6 o-layout__col-l-8 u-margin-bottom--30">
        <nn-input-field
          name="region"
          label="Regio (optioneel)"
          :id="'region-' + blockLabelsId" 
          :value="region"
          @change="onUpdateForeignAddressField"
          :addValidationIconPlaceholder="true"
        ></nn-input-field>
      </div>
    </div>
  </div>
</template>
<script>
import { error } from '@nn/psdl-utility/src/console'
import addressFinder from './addressFinder';
import validationMixin from '../../app-vue-shared/validationMixin'
import validations from './addressValidations'

const countries = require('../data/countries.json');

export default {
  name: 'address-block',
  mixins: [ validationMixin(validations) ],
  data: function () {
    return {
      formFilled: false,
      findAddressError: false,
      addressNotFound: false,
      countries,
      addressLineTwo: '',
      addressLineOne: '',
      region: ''
    }
  },
  components: {
    addressFinder,
  },
  props: {
    address: {
      type: Object,
      required: true,
    },
    title: String,
    blockLabelsId: {
      type: String,
      required: true,
    },
  },
  mounted: function(){
    if(this.address.postalCode && this.address.houseNumber){
      this.formFilled = true;
    }
    this.checkRequiredValidation()
  },
  beforeDestroy: function(){
    this.$emit('onFieldsValidated', {isValid: true});
  },
  methods: {
    validatePostCodeAfterCheck: function(elemName, elemValue){
      if(this.address.countryCode==='NL'){
        this.validateFormElement(elemName, elemValue);
      } else if(elemValue.trim().length >= 4){
        this.validationResults[elemName] = {
          valid: true,
          message: ""
        }
      } else {
        this.validationResults[elemName] = {
          valid: false,
          message: "Vul je postcode in."
        }
      }
    },
    onUpdateAddressField: function (value, event){
      this.formFilled = false;
      this.$emit('onAddressInput', {[event.target.name]: event.target.value});
      this.checkRequiredValidation();
    },
    onUpdateForeignAddressField: function () {
      const foreignAddress = `${this.addressLineOne} ${this.addressLineTwo} ${this.region}`;
      this.$emit('onAddressInput', {street: foreignAddress});
      this.checkRequiredValidation();
    },
    onUpdateSelectedCountry: function (value) {
      this.formFilled = false;
      this.resetAddressBlock();
      this.$emit('onAddressInput', {countryCode: value});
    },
    onAddressFound: function ({street, city}) {
      this.formFilled = true;
      this.$emit('onFieldsValidated', {isValid: true});
      this.addressNotFound = false;
      this.findAddressError = false;
      this.$emit('onAddressFound', { street, city });
    },
    onAddressFoundError: function () {
      this.addressNotFound = false;
      this.findAddressError = true;
      return error;
    },
    onAddressNotFound: function () {
      this.formFilled = false;
      this.addressNotFound = true;
      this.findAddressError = false;
    },
    onFormValidation: async function () {
      let valid = !this.findAddressError && await this.$validator.validate();
      return valid;
    },
    runValidations: function(){
      this.validatePostCodeAfterCheck('postalCode', this.address.postalCode);
      if(this.address.countryCode==='NL'){
        this.validateFormElement('houseNumber', this.address.houseNumber);
        if(this.addressNotFound){
          this.validateFormElement('city', this.address.city);
          this.validateFormElement('street', this.address.street);
        }
      } else {
        this.validateFormElement('addressLineOne', this.addressLineOne);
        this.validateFormElement('city', this.address.city);
      }
      return this.checkRequiredValidation();
    },
    checkRequiredValidation: function(){
      let valid = false;
      if(this.address.countryCode==='NL'){
        if(!this.formFilled){
          valid = false;
        } else if(!this.findAddressError && this.isFormFieldValid(['houseNumber','city','street'])) {
          valid = true
        } else {
          valid = false;
        }
        if(this.addressNotFound && this.address.street && this.address.city){
          valid = true;
        }
      }else if(this.isFormFieldValid(['addressLineOne','city'])){
        valid = true;
      }
      this.$emit('onFieldsValidated', {isValid: valid});
      return valid;
    },

    isFormFieldValid: function(fields){
      for (let i = 0; i < fields.length; i++) {
        const elementName = fields[i];
        if(!this.validationResults[elementName].valid){
          return false;
        }
      }
      return true;
    },
    resetAddressBlock: function () {
      this.$emit('onResetAddress');
      this.findAddressError = false;
      this.addressNotFound = false;
      this.addressLineTwo = '';
      this.addressLineOne = '';
      this.region = '';
    },
  },
};
</script>