<template>
  <div style="position:relative;" class="o-layout" data-test="collective">
    <div class="o-layout__row">

      <div class="o-layout__col-xs-2">
        <div class="u-margin-bottom--30">
          <nn-input-field
            label="Collectiviteiten"
            name="collective"
            @input="updateProperty('collective', $event)"
            @blur="validateFormElement('collective', $event.target.value)"
            @keydown-enter="validateFormElement('collective', $event.target.value)"
            :value="collective.collective"
            :readonly="locked || !isAdmin"
            :maxlength="16"
            :invalid="!isValidatedAndValid('collective')"
            :error="validationMessage('collective')"
          ></nn-input-field>
        </div>
      </div>

      <div class="o-layout__col-xs-4">
        <div class="u-margin-bottom--30">
          <nn-input-field
            label="Bedrijfsnaam"
            name="companyName"
            :value="collective.companyName"
            @input="updateProperty('companyName', $event)"
            :readonly="!isAdmin"
          ></nn-input-field>
        </div>
      </div>

      <div class="o-layout__col-xs-2">
        <div class="u-margin-bottom--30">
          <nn-input-field
            :readonly="!isAdmin"
            label="RC-nummer"
            name="rcNumber"
            :value="collective.rcNumber"
            @input="updateProperty('rcNumber', $event)"
            :maxlength="6"></nn-input-field>
        </div>
      </div>

      <div class="o-layout__col-xs-2">
        <div class="u-margin-bottom--30 qNumber">
          <nn-input-field
            label="Q-nummer"
            name="qNumber"
            :value="collective.qNumber"
            @input="updateProperty('qNumber', $event)"
            :maxlength="11"
            :readonly="!isAdmin">
          </nn-input-field>
        </div>
      </div>

      <div class="o-layout__col-xs-2">
        <div class="u-margin-bottom--30">
          <nn-dropdown
            :value="collective.mutation"
            @change="updateProperty('mutation', $event)"
            :options="options"
            :id="_uid + 'afstand'"
            name="mutation"
            label="Rol"
            :placeholder="newAccount ? '' : 'N.v.t.'"
            :readonly="!isAdmin"
          ></nn-dropdown>
        </div>
      </div>

      <div v-if="isAdmin" style="position:absolute; right: -20px; top: 30px;">
        <a @click.prevent="remove">
          <span aria-hidden="true" class="c-icon u-font-size-l c-icon--delete u-margin-left--10"></span>
        </a>
      </div>

    </div>
  </div>
</template>

<script>
  import ValidationMixin from '@nn/psdl-modules/src/app-vue-shared/validationMixin';

  export default {
    props: {
      id: { type: String },
      collective: {
        type: Object,
        required: true,
      },
      locked: { type: Boolean, default: false },
      valid: { type: Boolean, default: true },
      isAdmin: { type: Boolean, default: true },
      duplicates: { type: Array, default: [] },
      newAccount: Boolean,
    },
    mixins: [ValidationMixin({
      collective: value => ({
        valid: !!value && !!value.trim(),
        message: 'Geeft een collectiviteit op'
      })
    })],
    data () {
      return {
        options: [
          { label: 'N.v.t.', value: '' },
          { label: 'Muteren', value: 'M' },
          { label: 'Inzien', value: 'I' }
        ]
      };
    },
    methods: {
      updateProperty (name, value) {
        this.$store.commit('updateCollectiveProperty', { id: this.id, name, value })
        if (name === 'collective') {
          this.$store.commit('updateCollectiveValidity', { id: this.id, valid: !!value })
        }
      },
      remove () {
        this.$store.commit('removeCollective', this.id)
      },
    },
  };
</script>
