function asString(value) {
  if (typeof value === 'string') {
    return value;
  }

  return '';
}


export function fullName(firstName, familyName, familyNamePrefix) {
  let name = '';

  name += asString(firstName) + ' ';
  if (asString(familyNamePrefix)) {
    name += asString(familyNamePrefix) + ' ';
  }
  name += asString(familyName);

  return name;
}