
import { initialStoreState } from './store';

const updateAddressRow = (state, newAddress) => {
  state[newAddress.addressKey][newAddress.addressType] = newAddress.address;
}
const updateAddressFields = (state, {key, type, addressFields}) => {
  state[key][type] = {
    ...state[key][type],
    ...addressFields,
  };
}
const setInitialCompanyData = (state, intialCompanyData) => {
  state.oldAddress = intialCompanyData.address;
  state.kvk = intialCompanyData.kvk;
  state.name = intialCompanyData.name;
}
const setResults = (state, results) => {
  state.results = results;
}
const updateStatusFind = (state, newStatus) => {
  state.status.find = newStatus;
}
const updateBusinessName = (state, newName) => {
  state.name = newName;
}
const updateStartDate = (state, date) => {
  state.startDate = date;
}
const updateContact = (state, newContact) => {
  state.contact = newContact;
}
const updateAddressToModify = (state, {name, value}) => {
  state.addressesToModify[name] = value;
}
const updateStatusSendForm = (state, newStatus) => {
  state.status.sendForm = newStatus;
}
const resetAddress = (state, {key, type }) => {
  state[key][type].street = '';
  state[key][type].city = '';
  state[key][type].postalCode = '';
  state[key][type].houseNumber = '';
  state[key][type].houseNumberAddition = '';
  state[key][type].pOBox = '';
}
const resetState = (state) => {
  const newState = { ...initialStoreState(),};
  Object.keys(newState).forEach(key => state[key] = newState[key]);
}

export {
  updateAddressRow,
  updateAddressFields,
  setInitialCompanyData,
  setResults,
  updateStatusFind,
  updateBusinessName,
  updateStartDate,
  updateContact,
  updateAddressToModify,
  updateStatusSendForm,
  resetAddress,
  resetState,
}