<template>
  <form class="userform" @submit.prevent="validateFormAndSubmit" novalidate>
    <h1>{{`Account ${newAccount ? 'aanmaken' : 'wijzigen'} voor PSO/PAO/ISO`}}</h1>
    <nn-stepper :steps="steps" :activeStep="0"></nn-stepper>

    <div v-if="errorMessages.length > 0">
      <alert type="error" class="u-margin-bottom--30">
        <span v-for="(msg, i) in errorMessages" :key="i=msg">{{msg}}</span>
      </alert>
      <nn-button variant="back" class="u-margin-top--20" to="/">Terug naar het startscherm</nn-button>
    </div>

    <nn-spinner v-if="status === 'loading'"></nn-spinner>

    <div v-if="status === 'done'" class="o-layout form-fields">
      <div class="o-layout__row">
        <div class="o-layout__col-xs-9">
          <fieldset class="c-form-fieldset">
            <div class="o-layout">
              <div class="o-layout__row">
                <div class="o-layout__col-xs-6 o-layout__col-m-4">
                  <div class="u-margin-bottom--30">
                    <nn-input-field
                      :readonly="!newAccount && !isAdmin"
                      label="Voornaam"
                      name="firstName"
                      :value="account.firstName"
                      @input="onPropertyChanged('firstName', $event)"
                      @blur="validateFormElement('firstName', $event.target.value)"
                      @keydown-enter="validateFormElement('firstName', $event.target.value)"
                      :invalid="!isValidatedAndValid('firstName')"
                      :error="validationMessage('firstName')"
                    ></nn-input-field>
                  </div>
                </div>

                <div class="o-layout__col-xs-6 o-layout__col-m-4">
                  <div class="u-margin-bottom--30">
                    <nn-input-field
                      :readonly="!newAccount && !isAdmin"
                      label="Tussenvoegsel (optioneel)"
                      name="middleName"
                      :value="account.middleName"
                      @input="onPropertyChanged('middleName', $event)"
                    ></nn-input-field>
                  </div>
                </div>
              </div>
              <div class="o-layout__row">
                <div class="o-layout__col-s-8">
                  <div class="u-margin-bottom--30">
                    <nn-input-field
                      :readonly="!newAccount && !isAdmin"
                      label="Achternaam"
                      name="lastName"
                      :value="account.lastName"
                      @input="onPropertyChanged('lastName', $event)"
                      @blur="validateFormElement('lastName', $event.target.value)"
                      @keydown-enter="validateFormElement('lastName', $event.target.value)"
                      :invalid="!isValidatedAndValid('lastName')"
                      :error="validationMessage('lastName')"
                      required
                    ></nn-input-field>
                  </div>

                  <div class="u-margin-bottom--30">
                    <nn-input-field
                      :readonly="!newAccount"
                      label="Gebruikersnaam"
                      name="userName"
                      :value="account.userName"
                      @input="onPropertyChanged('userName', $event)"
                      @blur="validateFormElement('userName', $event.target.value)"
                      @keydown-enter="validateFormElement('userName', $event.target.value)"
                      :invalid="!isValidatedAndValid('userName')"
                      :error="validationMessage('userName')"
                      :minlength="2"
                      :maxlength="50"
                      required
                    ></nn-input-field>
                  </div>

                  <div class="u-margin-bottom--30">
                    <nn-input-field
                      :readonly="!newAccount && !isAdmin"
                      label="Telefoonnummer (optioneel)"
                      name="phone"
                      :value="account.phone"
                      @input="onPropertyChanged('phone', $event)"
                      type="tel"
                    ></nn-input-field>
                  </div>

                  <div class="u-margin-bottom--30">
                    <nn-input-field
                      :readonly="!newAccount && !isAdmin"
                      label="E-mailadres"
                      name="email"
                      :value="account.email"
                      @input="onPropertyChanged('email', $event)"
                      @blur="validateFormElement('email', $event.target.value)"
                      @keydown-enter="validateFormElement('email', $event.target.value)"
                      :invalid="!isValidatedAndValid('email')"
                      :error="validationMessage('email')"
                      type="email"
                      required
                    ></nn-input-field>
                  </div>

                  <div class="u-margin-bottom--20">
                    <div v-if="newAccount">
                      <nn-radio-button
                        @change="onPropertyChanged('accountType', $event)"
                        :model="account.accountType"
                        value="PSO"
                      >Beheer PSO</nn-radio-button>
                      <nn-radio-button
                        @change="onPropertyChanged('accountType', $event)"
                        :model="account.accountType"
                        value="PAO"
                      >Beheer PAO</nn-radio-button>
                      <nn-radio-button
                        @change="onPropertyChanged('accountType', $event)"
                        :model="account.accountType"
                        value="ISO"
                      >Beheer ISO</nn-radio-button>
                    </div>
                    <div v-else>
                      <nn-input-field
                        readonly
                        label="Account type"
                        name="accountType"
                        :value="account.accountType"
                      ></nn-input-field>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </fieldset>
        </div>

        <div class="o-layout__col-s-3" v-if="!newAccount">
          <h2 class="h2--secondary">Uw acties</h2>
          <ul class="c-list">
            <li class="c-list__item">
              <nn-link @click.prevent="openPasswordResetModal">Wachtwoord reset</nn-link>
            </li>
          </ul>
        </div>
      </div>

      <div class="o-layout__row">
        <div class="o-layout__col-xs-12 collective-list" data-test="collectiveList">
          <collective
            :is-admin="newAccount || isAdmin"
            :locked="col.locked && !newAccount"
            v-for="col in collectives"
            :id="col.id"
            :key="col.id"
            :duplicates="getDuplicates()"
            :collective="col.data"
            :new-account="newAccount"
            ref="collectives"
          ></collective>
        </div>
      </div>

      <div v-if="newAccount || isAdmin" class="o-layout__row">
        <div class="o-layout__col-xs-12 u-margin-bottom--30">
          <nn-button
            variant="dashed"
            @click="addCollective"
            class="u-width-100p u-textalign-left add-collective-button"
          >Collectiviteit toevoegen</nn-button>
        </div>
      </div>

      <div class="o-layout__row">
        <div class="o-layout__col-xs-12">
          <div class="o-layout__row">
            <div class="o-layout__col-xs-6">
              <nn-button
                v-if="newAccount"
                variant="secondary"
                @click="cancel"
              >Annuleren</nn-button>

              <nn-button
                v-else
                variant="back"
                :href="config.redirectCorporateSearch"
              >Terug naar zoeken</nn-button>
            </div>

            <div v-if="newAccount || isAdmin" class="o-layout__col-xs-6 u-textalign-right submit">
              <nn-button
                class="save-button"
                type="submit">{{newAccount ? 'Account aanmaken' : 'Opslaan'}}
              </nn-button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <nn-modal position="middle" ref="PsoAccPasswordReset" title="Wachtwoord resetten">
      <p>
        Weet u het zeker, dat u het wachtwoord van
        <strong>{{getFullName(account)}}</strong> wilt resetten?
      </p>
      <div class="o-layout">
        <div class="o-layout__row">
          <div class="o-layout__col-xs-6">
            <nn-button variant="secondary" @click="closeResetPasswordModal">Annuleren</nn-button>
          </div>
          <div class="o-layout__col-xs-6 u-textalign-right">
            <nn-button @click="resetAccount">Reset</nn-button>
          </div>
        </div>
      </div>
    </nn-modal>
  </form>
</template>

<script>
import { mapState } from 'vuex';

import Alert from '@nn/psdl-modules/src/app-vue-shared/alert';
import ValidationMixin from '@nn/psdl-modules/src/app-vue-shared/validationMixin';

import validations from './validations';
import { stepsAddUser, stepsEditUser } from './steps';

import Api from '../../services/api.service';
import Collective from '../../components/collective';

const api = new Api();

function createUniqueId () {
  let dt = new Date().getTime();
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'
    .replace(/[xy]/g, c => {
      const r = (dt + Math.random()*16)%16 | 0;
      dt = Math.floor(dt/16);
      return (c === 'x' ? r : (r&0x3|0x8)).toString(16);
    });
}

export default {
  name: 'userform',
  components: { Alert, Collective },
  mixins: [ValidationMixin(validations)],
  props: {
    newAccount: Boolean,
    config: Object,
  },
  data () {
    return {
      status: this.newAccount ? 'done' : 'initial',
      isAdmin: true,
      markedForDeleteCollectives: [],
      errorMessages: [],
    }
  },
  computed: {
    ...mapState(['account', 'collectives']),
    steps () {
      return this.newAccount ? stepsAddUser() : stepsEditUser(this.account.userName);
    },
  },
  methods: {
    onPropertyChanged (name, value) {
      this.$store.commit('updateAccountProperty', { name, value })
    },
    cancel () {
      this.$router.push({ name: 'home' });
    },
    async validateFormAndSubmit() {
      const collectiveRefs = this.$refs.collectives || [];
      collectiveRefs.forEach(collectiveRef => {
        collectiveRef.validateForm(collectiveRef.collective);
      });
      const allCollectivesValid = collectiveRefs.every(collectiveRef => collectiveRef.isFormValid);

      this.validateForm({ ...this.account });

      if (this.isFormValid && allCollectivesValid) {
        this.submitForm();
      } else {
        await this.$nextTick();
        const elem = document
          .querySelector('form.userform input.c-form-text--error')
        if (elem) {
          elem.focus();
        }
      }
    },
    getDuplicates () {
      if (this.collectives.length < 2 || this.collectives.some(_collective => _collective.data.collective === null)) {
        return [];
      }

      const counter = this.collectives.reduce((_counter, coll) => {
        _counter[coll.id] = ++_counter[coll.id] || 0;
        return _counter;
      }, {});

      return this.collectives.filter(coll => counter[coll.id]);
    },
    hasDuplicates () {
      return this.getDuplicates().length > 0;
    },
    addCollective () {
      this.$store.commit('addCollective', {
        id: createUniqueId(),
        valid: false,
        data: {
          collective: null,
          companyName: '',
          rcNumber: '',
          qNumber: '',
          mutation: ''
        }
      });
    },
    openPasswordResetModal () {
      this.$refs.PsoAccPasswordReset.open();
    },
    closeResetPasswordModal () {
      this.$refs.PsoAccPasswordReset.close();
    },
    async loadData () {
      try {
        this.status = 'loading';
        const userResponse = await api.getUser(this.$route.params.id);
        const collectiveRes = await api.getUserCollectives(
          this.$route.params.id,
          userResponse.employerId
        );
        const collectives = collectiveRes.map(collective => {
          collective.locked = true;
          collective.valid = true;
          return collective;
        });
        this.$store.commit('updateAccount', userResponse);
        this.$store.commit('updateCollectives', collectives);
        this.isAdmin = userResponse.isAdmin === 'true';
        this.status = 'done';
      } catch (e) {
        if (e.messages) {
          this.errorMessages = e.messages
            .map(m => m.description);
        } else {
          this.errorMessages = ['Er is een onbekende fout opgetreden.']
        }
        this.status = 'error';
      }
    },
    async resetAccount () {
      try {
        await api.resetUser(this.account.userName);
      } catch (e) {
        if (e.messages) {
          this.errorMessages = e.messages.map(m => m.description);
        } else {
          this.errorMessages = ['Er is een onbekende fout opgetreden'];
        }
      } finally {
        this.closeResetPasswordModal();
      }
    },
    getFullName () {
      return `${this.account.firstName} ${this.account.middleName} ${this.account.lastName}`.replace('  ', ' ')
    },
    async submitForm () {
      try {
        this.$store.commit('updateAccount', this.account)
        this.$store.commit('updateCollectives', this.collectives)

        if (this.newAccount) {
          const employerId = await api.createUser(this.account);
          await api.createOrUpdateCollectives(this.collectives, employerId);
          this.$router.push({ name: 'confirmAddAccount' })
        } else {
          await api.updateUser(this.account, this.account.employerId);
          await api.deleteCollectives(
            this.markedForDeleteCollectives,
            this.account.employerId
          );
          await api.createOrUpdateCollectives(
            this.collectives,
            this.account.employerId
          );
          this.markedForDeleteCollectives = [];
          this.$router.push({ name: 'confirmEditAccount' });
        }

      } catch (e) {
        if (e.messages) {
          this.errorMessages = e.messages.map(m => m.description);
        } else {
          this.errorMessages = ['Er is een onbekende fout opgetreden'];
        }
      }
    },
  },
  mounted () {
    if (!this.newAccount) {
      this.loadData()
    }
  }
};
</script>