<template>
  <div>
    <h1 v-colored-text>Account wijzigen voor PSO/PAO/ISO</h1>
    <nn-stepper :steps="steps" :activeStep="1"></nn-stepper>
    <p>Er zijn wijzigingen doorgevoerd voor <strong>{{ getFullName() }}</strong>.</p>
    <div class="o-layout">
      <div class="o-layout__row">
      <div class="o-layout__col-xs-9">
        <nn-button variant="back" :href="config.redirectCorporateSearch">Terug naar accounts zoeken</nn-button>
        <nn-button @click="backToStart" class="u-float-right">Naar home PSO/PAO/ISO</nn-button>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex';
  import { stepsEditUser } from "./steps.js"

  import ColoredText from "./../../../app-vue-shared/coloredText.directive.js";
  import Datatable from "./../../../app-vue-shared/datatable.js";

  export default {
    name: 'ConfirmEditAccount',
    components: { Datatable },
    directives: { ColoredText },
    props: {
      config: Object
    },
    methods: {
      gotoEditAccount() {
        this.$router.push({name: 'editAccount'});
      },
      backToStart() {
        this.$store.commit('resetAccount');
        this.$router.push({name: 'home'});
      },
      getFullName() {
        return (this.account.firstName + ' ' + ((this.account.middleName && this.account.middleName.length > 0) ? this.account.middleName+ ' ' + this.account.lastName : this.account.lastName));
      },
    },
    computed: {
      ...mapState([
        'account', 'collectives'
      ]),
      steps () {
        return stepsEditUser(this.account.userName);
      },
    },
    created() {
      if (this.account.firstName === null) {
        this.gotoEditAccount();
      }
    }
  }
</script>
