export const findSelected = function(selected, results){
  return formatSelectedResult(results.find(row => row === selected), results.kvkNumber)
}

const formatSelectedResult = function (selected, kvkNumber){
  let formattedResult = {};
  formattedResult.kvk = kvkNumber;

  let addressObj = {};
  for (let [key, value] of Object.entries(selected.Address)) {
    let objKey = key.charAt(0).toLowerCase() + key.substring(1);

    if(typeof(value) === "object" && Object.keys(value).length === 0){
      addressObj[objKey] = '';
    } else {
      addressObj[objKey] = value;
    }
  }

  let postAddressObj = {
    street: '',
    postalCode: '',
    city: '',
    countryCode: 'NL',
    houseNumber: '',
    houseNumberAddition: '',
    pOBox: '',
  }

  formattedResult.mainAddress = addressObj;
  formattedResult.postAddress = postAddressObj;
  formattedResult.name = selected.Organisation.OrganisationName;

  if(!formattedResult.mainAddress.houseNumber && formattedResult.mainAddress.pOBox){
    formattedResult.mainAddress.houseNumber = formattedResult.mainAddress.pOBox;
    formattedResult.mainAddress.street = 'Postbus';
  }

  return formattedResult;
}