<template>
  <div>
    <div class="o-layout">
      <h2>Welke adreswijziging(en) wil je aan ons doorgeven?</h2>
      <nn-checkbox-group mode="vertical">
        <nn-checkbox
          v-for="checkbox in checkboxesAddressType"
          :key="checkbox.id"
          :name="checkbox.id"
          :value="addressesToModify[checkbox.id]"
          @change="updateModificationAddressType"
        >{{checkbox.label}}</nn-checkbox>
      </nn-checkbox-group>
      <div class="o-layout__row u-margin-bottom">
        <div class="o-layout__col-xs-12 o-layout__col-m-9">
          <nn-notification
            type="error"
            v-model="showNotificationNoAddressSelected"
          >Kies een adres dat je wilt wijzigen.</nn-notification>
        </div>
      </div>
      <hr />

      <transition name="slide">
        <div v-if="addressesToModify.mainAddress || addressesToModify.postAddress" class="o-layout__row u-margin-bottom--40">
          <div class="o-layout__col-xs-12 o-layout__col-m-4">
            <h2>Bedrijfsgegevens</h2>
            <nn-input-field
              name="name"
              id="business-name"
              :value="name"
              label="Bedrijfsnaam"
              :required="true"
              @input="updateBusinessName($event)"
              @blur="validateFormElement($event, 'Bedrijfsnaam')"
              @keyup-enter="validateFormElement($event, 'Bedrijfsnaam')"
              :invalid="!!formErrors.Bedrijfsnaam"
              error="Vul je bedrijfsnaam in"
              :readonly="kvk.length > 0"
            >
            </nn-input-field>
          </div>
        </div>
      </transition>

      <transition name="slide">
        <div v-if="addressesToModify.mainAddress || addressesToModify.postAddress && !addressesToModify.mainAddress" class="o-layout__row u-margin-bottom--40">
          <div v-if="!kvk" class="o-layout__col-xs-12 o-layout__col-m-6">
            <h2 v-if=!addressesToModify.mainAddress>Vestigingsadres</h2>
            <h2 v-else>Oud vestigingsadres</h2>
              <address-block
                ref="oldMainAddress"
                title="Oud adres"
                blockLabelsId="oldMainAddress"
                :address="oldAddress.mainAddress"         
                @onAddressInput="_updateAddressFields('oldAddress', 'mainAddress', $event)"
                @onAddressFound="_updateAddressFields('oldAddress', 'mainAddress', $event)"
                @onResetAddress="_resetAddress('oldAddress', 'mainAddress')" 
                data-test="oldMainAddressBlock"
                @onFieldsValidated="validatedAdressblock('oldMainAddress', $event)"
              ></address-block>
          </div>
          <div v-if="addressesToModify.mainAddress" class="o-layout__col-xs-12 o-layout__col-m-6">
            <h2>Nieuw vestigingsadres</h2>
            <address-block 
              ref="newMainAddress"
              title="Nieuw adres"
              blockLabelsId="mainAddress"
              :address="newAddress.mainAddress"
              @onAddressInput="_updateAddressFields('newAddress', 'mainAddress', $event)"   
              @onAddressFound="_updateAddressFields('newAddress', 'mainAddress', $event)"
              @onResetAddress="_resetAddress('newAddress', 'mainAddress')"
              data-test="newMainAddressBlock"
              @onFieldsValidated="validatedAdressblock('mainAddress', $event)"
            ></address-block>
          </div>
        </div>
      </transition>

      <transition name="slide">
        <div v-if="addressesToModify.postAddress" class="o-layout__row u-margin-bottom--40">
          <div class="o-layout__col-xs-12 o-layout__col-m-6">
            <h2>Nieuw postadres</h2>
            <address-block 
              ref="newpostAddress"
              title="Nieuw postadres"
              addressType="postAddress"
              blockLabelsId="postAddress"
              :address="newAddress.postAddress" 
              @onAddressInput="_updateAddressFields('newAddress', 'postAddress', $event)"
              @onAddressFound="_updateAddressFields('newAddress', 'postAddress', $event)"
              @onResetAddress="_resetAddress('newAddress', 'postAddress')"
              data-test="newPostAddressBlock"
              @onFieldsValidated="validatedAdressblock('postAddress', $event)"
            ></address-block> 
          </div>
        </div>
      </transition>

      <div v-if="addressesToModify.mainAddress || addressesToModify.postAddress" class="o-layout__row u-margin-bottom--40">
        <div class="o-layout__col-xs-12">
          <h2>Ingangsdatum adreswijziging</h2>
        </div>
        <div class="o-layout__col-xs-6 o-layout__col-m-4 u-margin-bottom--30">
          <datepicker
            :value="startDate"
            :validationMessage="formErrors.startDate"
            name="startDate"
            @change="_updateStartDate($event.target.value)"
          ></datepicker>
        </div>
        <hr />
      </div>
    </div>
    
    <div class="u-margin-top--20">
      <nn-button variant="back" :to="'/'">Terug</nn-button>
      <nn-button
        variant="next"
        name="forward"
        :to="'/contactdetails'"
        class="pull-right c-button--enabled"
        data-test="forwardButton"
      >
        Volgende stap
      </nn-button>
    </div>
  </div>
</template>

<script>
  import addressBlock from '../components/addressBlock';
  import datepicker from '../components/datepicker';
  import { mapState, mapActions } from 'vuex';

  export default {
    components: {
      addressBlock,
      datepicker,
    },
    provide() {
      return {
        $validator: this.$validator,
      }
    },
    data: function () {
      return {
        showNotificationNoAddressSelected: false,
        formErrors: {},
        checkboxesAddressType: [
          {
            id: 'mainAddress',
            label: 'Nieuw vestigingsadres',
          },
          {
            id: 'postAddress',
            label: 'Nieuw postadres',
          },
        ],
      }
    },
    computed: {
      ...mapState([
        'newAddress',
        'oldAddress',
        'kvk',
        'name',
        'results',
        'status',
        'startDate',
        'addressesToModify',
      ]),
    },
    methods: {
      ...mapActions([
        'updateAddressFields',
        'updateStartDate',
        'updateBusinessName',
        'updateAddressToModify',
        'resetAddress'
      ]),
      updateModificationAddressType: function (value, event){
        this.showNotificationNoAddressSelected = false
        this.updateAddressToModify({ name: event.target.name, value });
      },
      _updateAddressFields: function(key, type, addressFields) {
        this.updateAddressFields({key , type, addressFields});
      },
      _resetAddress: function(key, type) {
        this.resetAddress({ key, type })
      },
      _updateStartDate: function(value){
        this.formErrors.startDate = "";
        this.updateStartDate(value)
      },
      validateFormElement: function($event, elementName){
        this.formErrors = { ...this.formErrors, [elementName]: !event.target.value.trim() };
      },
      validatedAdressblock: function(addressType, eventData){
        this.formErrors = { ...this.formErrors, [addressType]: !eventData.isValid };
      },
      isFormDataValid: function(){
        if(!this.addressesToModify.mainAddress && !this.addressesToModify.postAddress) {
          this.showNotificationNoAddressSelected = true;
          return false
        }
        let valid = true;
        for (var ref in this.$refs) {
          if(this.$refs[ref] && !this.$refs[ref].runValidations() && valid){
            valid = false;
          }
        }
        if(this.addressesToModify.mainAddress || this.addressesToModify.postAddress){
          this.formErrors.startDate = this.startDate ? '' : 'Denkt u aan het invullen van dit veld';
          this.formErrors.Bedrijfsnaam = !this.name.trim();
        }
        for(var prop in this.formErrors) {
          if(this.formErrors.hasOwnProperty(prop)){
            if(this.formErrors[prop]){
              valid = false;
            }
          }
        }
        return valid;
      },
    },
    beforeRouteLeave(to, from, next) {
      if (to.name === 'contactdetails') {
        if(this.isFormDataValid()){
          next();
        }
      } else {
        next();
      }
    }
  }
</script>