import Vue from 'vue';
import VueComponents from '@nn/vue-components';
import { createVueChildContainer } from '../app-vue-shared/createVueChildContainer';
import Search from './search.js';
import '../app-vue-shared/shared.js';
import { store } from './store.js';

Vue.use(VueComponents);

const bootstrapCorporateSearchUsers = (root, config) => {
  new Vue({
    el: createVueChildContainer(root),
    store,
    render: h => h(Search, { props: { config } })
  });
}

export default bootstrapCorporateSearchUsers;
