import Vue from 'vue';
import Vuex from 'vuex';

import * as mutations from './mutations.js';
import * as actions from './actions.js';

Vue.use(Vuex);

const initialAddressState = () => {
  return {
    street: '',
    postalCode: '',
    city: '',
    countryCode: 'NL',
    houseNumber: '',
    houseNumberAddition: '',
    pOBox: '',
  }
};

const initialStoreState = () => {
  return {
    status: {
      find: 'initial',
      sendForm: 'initial',
    },
    addressesToModify: {
      mainAddress: false,
      postAddress: false,
    },
    results: [],
    oldAddress: {
      mainAddress: initialAddressState(),
    },
    newAddress: {
      mainAddress: initialAddressState(),
      postAddress: initialAddressState(),
    },
    startDate: '',
    contact: {
      firstName: '',
      preposition: '',
      lastName: '',
      phoneNumber: '',
      emailAddress: '',
    },
    kvk: '',
    name: '',
  };
};

const store = new Vuex.Store({
  state: initialStoreState(),
  mutations,
  actions
});

export { mutations, actions, store, initialStoreState, initialAddressState}
