<template>
  <div ref="contextDialog" class="u-inline-block" data-module="contextdialog" :data-config="dataConfig">
    <a href="#" data-role="context-dialog-link">
      <i class="c-icon c-icon--pict-info c-link--info"></i>
    </a>
    <div data-role="context-dialog-content" class="u-hidden">
      <slot></slot>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'content-dialog',
    props: {
      side: {
        type: String,
        default: 'top'
      },
      align: {
        type: String,
        default: 'bottom'
      },
    },
    computed: {
      dataConfig: function () {
        return JSON.stringify({
          side: this.side,
          align: this.align,
        });
      }
    },
    mounted() {
      window.nn.initializeComponents(this.$refs.contextDialog);
    },
  };
</script>

