import Vue from "vue";
import Vuex from 'vuex';
import http from '../app-utilities/http';
import { asArray } from "@nn/psdl-utility/src/object-utils";
import { fullName } from "./utilities";
import { error } from '@nn/psdl-utility/src/console.js';

Vue.use(Vuex);

const state = {
  message: '',
  status: '',
  businessUsers: [],
}

const mutations = {
  setBusinessUsers(state, businessUsers) {
    state.businessUsers = businessUsers.map((user) => {
      return {
        DLPortalsEmployerId: user.DLPortalsEmployerId,
        Customer_GR_ID: user.Customer_GR_ID,
        Username: user.Username,
        FirstName: user.FirstName,
        FamilyNamePrefix: user.FamilyNamePrefix,
        FamilyName: user.FamilyName,
        FullName: fullName(user.FirstName, user.FamilyName, user.FamilyNamePrefix),
        EmailAddress: user.Emailaddress || user.EmailAddress,
      };
    });
  },
  updateStatus(state, obj) {
    state.status = obj.status;
  },
  setMessage(state, obj) {
    state.message = obj.message;
  },
  clearState(state) {
    state.message = '';
    state.status = '';
    state.businessUsers = [];
  }
}

const actions = {
  clearState({ commit }) {
    commit('clearState')
  },

  async findBusinessUsers({ commit }, { searchType, searchInput }) {
    const options = {credentials: 'same-origin'}
    const url = '/api/c2/execute/WebEnvironment/SearchBusinessUserForBusinessCustomer';
    const body = {
      SearchBusinessUser_Request: {
        UsernameRequester: "?",
        UserCategory: "ALL",
        Filters: {
          Filter: {
            Element: searchType,
            Value: searchInput
          }
        }
      }
    };
    try {
      commit('updateStatus', { status: 'loading' })
      await http.post(url, body, options).then(response => {
        let businessUsers = asArray(response.SearchBusinessUser_Response.Results.BusinessUser)
        if (businessUsers) {
          commit('setBusinessUsers', businessUsers);
        } else {
          commit('setMessage', {message: 'Er is geen account gevonden.'})
        }
      });
    } catch (e) {
      error(e)
      commit('setMessage', { message: 'Er is een fout opgetreden. Probeer het later nog eens.'})
    } finally {
      commit('updateStatus', { status: 'done' })
    }
  }
}

const store = new Vuex.Store({
  state,
  mutations,
  actions,
})

export { mutations, actions, store }