import Vue from "vue";
import VueRouter from 'vue-router'

import findBusiness from '../pages/findBusiness';
import businessDetails from '../pages/businessDetails';
import contactDetails from '../pages/contactDetails';
import reviewAndSend from '../pages/reviewAndSend';

const createRouter = () => {
  // eslint-disable-next-line no-undef
  if(!TESTING) {
    // Only add this line if we are not unit testing, otherwise mocking of $route and $router will not work as this line
    // makes them read-only
    Vue.use(VueRouter);
  }
  return new VueRouter({
    routes: [
      { path: '/', name: 'find', component: findBusiness },
      { path: '/businessdetails', name: 'businessdetails', component: businessDetails},
      { path: '/contactdetails', name: 'contactdetails', component: contactDetails },
      { path: '/review', name: 'review', component: reviewAndSend },
      { path: '*', redirect: '/' }
    ]
  });
}

export default createRouter;
