<template>
  <user-form
    title="Aanpassen accountgegevens"
    :status="status"
    :onPropertyChanged="onPropertyChanged"
    :account="account"
    :onFormSubmit="submitForm"
    :errorMessages="errorMessages"
  >
     <h2>Aanpassen beheer collectiviteiten</h2>
      <div class="o-layout">
        <div class="o-layout__row">
          <div class="o-layout__col-xs-12 collectivityTable">
            <Datatable
              :rows="collectives"
              :showPagination="false"
              :border="false"
              :displayedColumns="columns"
              :multiSelect="true"
              :selection="getSelection()"
              @updateSelection="updateSelection"
            ></Datatable>
          </div>
        </div>
        <div class="o-layout__row">
          <div class="o-layout__col-xs-6 o-layout__col-m-3">
            <nn-button variant="secondary" @click="cancel">Annuleren</nn-button>
          </div>
          <div class="o-layout__col-xs-6 o-layout__col-m-3 u-textalign-right">
            <nn-button type="submit" data-test="edit-submitButton">Opslaan</nn-button>
          </div>
        </div>
      </div>
  </user-form>
</template>

<script>
  import Api from '../../services/api.service';
  import { error } from '@nn/psdl-utility/src/console';
  import Datatable from '../../../app-vue-shared/datatable';
  import UserForm from './Form';
  const api = new Api();

  export default {
    name: 'EditAccount',
    components: { Datatable, UserForm },
    data () {
      return {
        account: {
          firstName: null,
          middleName: null,
          lastName: null,
          userName: null,
          phone: null,
          email: null
        },
        employerId: '',
        columns: {
          'collective': 'Collectiviteiten',
          'companyName': 'Bedrijfsnaam',
          'qNumber': 'Q-nr',
          'rcNumber': 'RC-nr',
          'mutation': 'Rol'
        },
        collectives: [],
        checkedCollectives: [],
        status: 'initial',
        authorisation: null,
        errorMessages: [],
      };
    },
    methods: {
      onPropertyChanged(name, value) {
        this.account = {
          ...this.account,
          [name]: value,
        };
      },
      async loadData () {
        try {
          this.status = 'loading';
          const userName = this.$route.params.id || '';
          this.account = await api.getUser(userName);

          const adminData = await api.getAdminData();
          this.employerId = adminData.employerId;
          this.collectives = adminData.collectives;
          this.authorisation = adminData.authorisations;

          this.checkedCollectives = await api.getUserCollectives(userName);
          this.status = 'done';
        } catch (e) {
          if (e.messages) {
            this.errorMessages = [ e.messages[0].description ];
          } else {
            this.errorMessages = [ 'Er is een onbekende fout opgetreden.' ];
            error(e);
          }
          this.status = 'error';
        }
      },
      async submitForm() {
        try {
          await api.updateUser(this.account, this.employerId, this.checkedCollectives, this.authorisation);
          this.$router.push({ name: 'home' });
        } catch (e) {
          if (e.messages) {
            this.errorMessages = e.messages.map(m => m.description);
          } else {
            this.errorMessages = ['Er is een onbekende fout opgetreden'];
            error(e);
          }
        }
      },
      isCollectiveChecked (id) {
        return (this.checkedCollectives.indexOf(id) > -1);
      },
      getSelection () {
        return this.checkedCollectives;
      },
      updateSelection (selection) {
        this.checkedCollectives = selection;
      },
      cancel () {
        this.$router.push({ name: 'home' });
      },
    },
    created () {
      this.loadData();
    }
  }
  </script>
