const emailRegex = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
// eslint-disable-next-line no-useless-escape
const userNameRegex = /^[a-zA-Z0-9-_.]+$/;
const hasValue = (value) => value ? !!value.trim() : !!value;

export default {
  firstName: (value) => ({ valid: hasValue(value), message: 'Geef een voornaam op voor de gebruiker' }),
  lastName: (value) => ({ valid: hasValue(value), message: 'Geef een achternaam op voor de gebruiker' }),
  userName: (value) => {
    if (hasValue(value) && value.trim().length < 2) {
      return { valid: false, message: 'Je gebruikersnaam moet tussen de 2 en 50 karakters bevatten.' };
    }
    if (!userNameRegex.test(value)) {
      return { valid: false, message: 'De toegestane tekens zijn a-z, A-Z, 0-9 en . - _ ' };
    }
    return { valid: hasValue(value), message: 'Geef een gebruikersnaam op waarmee de gebruiker kan inloggen' };
  },
  email (value) {
    if (!hasValue(value)) {
      return { valid: false, message: 'Geef een e-mailadres op waar we het account naar toe kunnen sturen' };
    }
    return { valid: emailRegex.test(value), message: 'Vul een geldig e-mailadres in' };
  },
}