import Vue from "vue";
import VueRouter from 'vue-router'

import Home from '../pages/home';

import Form from '../pages/account/Form';

import ConfirmAddAccount from '../pages/account/confirm-add';
import ConfirmEditAccount from '../pages/account/confirm-edit';

const createRouter = () => {
  if(!TESTING) {
    // Only add this line if we are not unit testing, otherwise mocking of $route and $router will not work as this line
    // makes them read-only
    Vue.use(VueRouter);
  }
  return new VueRouter({
    routes: [
      { path: '/', name: 'home', component: Home },
      { path: '/add', name: 'addAccount', component: Form, props: { newAccount: true } },
      { path: '/confirmAdd', name: 'confirmAddAccount', component: ConfirmAddAccount },
      { path: '/edit/:id', name: 'editAccount', component: Form },
      { path: '/confirmEdit', name: 'confirmEditAccount', component: ConfirmEditAccount },
    ],
    scrollBehavior () {
      return { x: 0, y: 0 }
    }
  });
}

export default createRouter;
