import { error } from '@nn/psdl-utility/src/console.js';
import { findTheBusinesses, sendRequestMovementForm } from '../services/api';
import { findSelected } from '../services/selected';

const findBusinesses = async ({ commit }, searchInput) => {
  try {
    commit('updateStatusFind', 'loading');
    const result = await findTheBusinesses(searchInput);
    if (result.length > 0) {
      result.kvkNumber = searchInput;
      commit('setResults', result);
      commit('updateStatusFind', 'done');
    } else {
      commit('setResults', []);
      commit('updateStatusFind', 'not-found');
    }
  } catch (e) {
    error(e);
    commit('setResults', []);
    commit('updateStatusFind', 'error');
  }
}
const setInitialCompanyData = ({ commit, state }, selected) => {
  const selectedCompany = findSelected(selected, state.results);
  const companyData = {
    'address': {
      'mainAddress': selectedCompany.mainAddress,
      'postAddress': selectedCompany.postAddress,
    },
    'kvk': selectedCompany.kvk,
    'name': selectedCompany.name,
  }
  commit('setInitialCompanyData', companyData);
}

const updateAddressFields = ({ commit }, {key, type, addressFields}) => {
  commit('updateAddressFields', {key, type, addressFields});
}

const updateBusinessName = ({ commit }, newName) => {
  commit('updateBusinessName', newName);
}

const updateStartDate = ({ commit }, newDate) => {
  commit('updateStartDate', newDate);
}

const updateContact = ({ commit }, newContact) => {
  commit('updateContact', newContact);
}

const updateAddressToModify = ({ commit }, {name, value}) => {
  commit('updateAddressToModify', {name, value});
}

const updateStatusSendForm = ({ commit }) => {
  commit('updateStatusSendForm', 'initial');
}

const resetAddress = ({ commit }, { key, type }) => {
  commit('resetAddress', { key, type });
}

const resetState = ({ commit }) => {
  commit('resetState');
}

const sendFinalForm = async ({ commit, state }) => {
  try {
    commit('updateStatusSendForm', 'loading');
    const result = await sendRequestMovementForm(state);
    if (result) {
      commit('updateStatusSendForm', 'done');
    }
  } catch (e) {
    error(e);
    commit('updateStatusSendForm', 'error');
  }
}

export {
  findBusinesses,
  setInitialCompanyData,
  updateAddressFields,
  updateBusinessName,
  updateStartDate,
  updateContact,
  updateAddressToModify,
  updateStatusSendForm,
  resetState,
  resetAddress,
  sendFinalForm,
}
