<template>
  <div>
    <nn-stepper :steps="steps" :activeStep="currentStep"></nn-stepper>
    <router-view></router-view>
  </div>
</template>
<script>
  export default {
    data: function () {
      return {
        steps: [
          { name: 'Zoek bedrijf', to: 'find' },
          { name: 'Bedrijfsgegevens', to: 'businessdetails' },
          { name: 'Contactgegevens', to: 'contactdetails' },
          { name: 'Controleer & Verzend', to: 'review' },
        ],
      }
    },
    computed: {
      currentStep: function () {
        const step = this.steps.find(s => this.$route && s.to === this.$route.name);
        return this.steps.indexOf(step);
      }
    }
  }
</script>