<template>
  <div class="o-layout o-layout__container">
    <div class="o-layout__row">
      <alert
        v-if="successMessage"
        :class="'u-margin-bottom--30 u-padding-top--25'"
        :type="'success'"
        @closeAlert="successMessage = null"
      >{{successMessage}}</alert>
      <div class="o-layout__col-s-9"></div>
      <div class="o-layout__col-s-3" v-if="errorCode !== 'NOT-ALLOWED'">
        <h2 class="h2--secondary">Uw acties</h2>
        <ul class="c-list">
          <li class="c-list__item">
            <nn-link to="/add">Nieuwe gebruiker</nn-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="o-layout__row">
      <nn-spinner v-if="status === 'loading'"></nn-spinner>
      <alert v-if="warningMessage" :type="'warning'">
          {{warningMessage}}
      </alert>
      <alert v-if="errorMessage" :type="'error'">
          {{errorMessage}}
      </alert>
      <Datatable v-if="status === 'loaded'" :elementsPerPage="100" :rows="rows" :displayedColumns="columns" :crud="true">
          <template slot-scope="item">
            <a @click.prevent="gotoEdit(item.Username)"><span aria-hidden="true" class="c-icon u-font-size-l c-icon--edit"></span></a>
            <a @click.prevent="openModal(item)"><span aria-hidden="true" class="c-icon u-font-size-l c-icon--delete u-margin-left--10"></span></a>
          </template>
      </Datatable>
    </div>
    <nn-modal variant="middle" ref="PsoDeleteUser" title="Gebruiker verwijderen">
      <div class="o-layout">
          <div class="o-layout__row">
            <div class="o-layout__col-xs-12">
              <p>Weet u het zeker, dat u het account van <strong>{{_getFullName(modalData)}}</strong> wilt verwijderen? Het is voor {{_getFullName(modalData)}} dan niet meer mogelijk om in te loggen.</p>
            </div>
          </div>
          <div class="o-layout__row">
            <div class="o-layout__col-xs-6">
                <nn-button variant="secondary" @click="closeModal">Annuleren</nn-button>
            </div>
            <div class="o-layout__col-xs-6 u-textalign-right">
                <nn-button @click="deleteUser">Verwijderen</nn-button>
            </div>
          </div>
        </div>
    </nn-modal>
  </div>
</template>

<script>
  import Api from "./../services/api.service.js";

  import Alert from "./../../app-vue-shared/alert.js";
  import Datatable from "./../../app-vue-shared/datatable.js";

  import { error } from '@nn/psdl-utility/src/console.js';

  const api = new Api();

  export default {
    components: { Datatable, Alert },
    data() {
      return {
        rows: [],
        columns: { 'Naam': 'Naam', 'Gebruikersnaam': 'Gebruikersnaam', 'crud': '' },
        modalData: {},
        status: 'initial',
        errors: [],
        errorMessage: null,
        warningMessage: null,
        errorCode: null,
        successMessage: null,
      };
    },
    methods: {
      async loadData () {
        try {
          this.errorMessage = null;
          this.warningMessage = null;

          this.rows = [];
          this.status = 'loading';

          const response = await api.searchUsers()
          if (response.length) {
            response.map(item => {
              if (item.Admin === 'false') {
                this.rows.push({ 'Naam': this._getFullName(item), 'Gebruikersnaam': item.Username, 'crud': item });
              }
            });
          } else {
            this.warningMessage = 'Geen accounts gevonden.';
          }
        } catch (e) {
          if (e.messages) {
            this.errorCode = e.messages[0].code;
            this.errorMessage = e.messages[0].description;
          } else {
            this.errorMessage = 'Er is een onbekende fout opgetreden.';
            error(e);
          }
        } finally {
          this.status = 'loaded';
        }
      },
      async deleteUser () {
        try {
          await api.deleteUser(this.modalData.Username);
          this.successMessage = this.modalData.Username + ' is verwijderd.';
          await this.loadData();
        } catch (e) {
          if (e.messages) {
            this.errorMessage = e.messages[0].description;
          } else {
            this.errorMessage = 'Er is een onbekende fout opgetreden';
            error(e);
          }
        } finally {
          this.closeModal();
        }
      },
      gotoEdit (username) {
        this.$router.push({path: ('/edit/' + username) });
      },
      openModal (item) {
        this.modalData = item;
        this.$refs.PsoDeleteUser.open();
      },
      closeModal () {
        this.$refs.PsoDeleteUser.close();
      },
      _getFullName (item) {
        return item.FirstName + ' ' + ((item.FamilyNamePrefix && item.FamilyNamePrefix.length > 0) ? item.FamilyNamePrefix+ ' ' + item.FamilyName : item.FamilyName);
      }
    },
    created () {
      this.loadData();
    }
  };
</script>
